<template>
  <div class="innerbox">
    <div class="secA">
      <div class="secA_1">
        <h3>今日值班人员</h3>
        <hr />
        <div class="d-flex">
          <div class="optA ml_144">
            <el-image :style="imgStyle" :src=" dutyList && dutyList[0].imgUrl" :fit="fit"></el-image>
            <div class="opt_name">
              姓名:
              <span>{{dutyList[0].personReal}}</span>
            </div>
            <div class="opt_phone">
              电话:
              <span>{{dutyList[0].tel}}</span>
            </div>
          </div>
          <div class="optB ml_173">
            <el-image :style="imgStyle" :src=" dutyList && dutyList[1].imgUrl" :fit="fit"></el-image>
            <div class="opt_name">
              姓名:
              <span>{{dutyList[1].personReal}}</span>
            </div>
            <div class="opt_phone">
              电话:
              <span>{{dutyList[1].tel}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="secA_2">
        <h3>明日值班人员</h3>
        <hr />
        <div class="d-flex">
          <div class="optA ml_144">
            <el-image :style="imgStyle" :src=" dutyList && dutyList[2].imgUrl" :fit="fit"></el-image>
            <div class="opt_name">
              姓名:
              <span>{{dutyList && dutyList[2].personReal}}</span>
            </div>
            <div class="opt_phone">
              电话:
              <span>{{dutyList && dutyList[2].tel}}</span>
            </div>
          </div>
          <div class="optB ml_173">
            <el-image :style="imgStyle" :src=" dutyList && dutyList[3].imgUrl" :fit="fit"></el-image>
            <div class="opt_name">
              姓名:
              <span>{{dutyList && dutyList[3].personReal}}</span>
            </div>
            <div class="opt_phone">
              电话:
              <span>{{dutyList && dutyList[3].tel}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="secB">
      <div>
        <h3>历史值班人员</h3>
        <el-table
          :data="dutyList"
          stripe
          height=""
          style="width: 90%; font-size:16px; margin:0 auto;"
          :header-row-style="{height:'3.44rem'}"
          :row-style="{height:'3.44rem'}"
          :header-cell-style="{background:'#F5F7FA',color:'#0d0d0d','font-size':'18px'}"
        >
          <el-table-column align="center" fixed prop="date" label="值班时间"></el-table-column>
          <el-table-column align="center" fixed prop="address" label="值班地点"></el-table-column>
          <el-table-column align="center" prop="name" label="值班人员"></el-table-column>
          <el-table-column align="center" prop="tel" label="电话号码"></el-table-column>
          <!-- <el-table-column align="center" prop="name2" label="值班人员2"></el-table-column>
          <el-table-column align="center" prop="phone2" label="电话号码2"></el-table-column>-->
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["intervalTime"],
  data() {
    return {
      imgStyle: { width: "114px", height: "142px" },
      dutyList: [],
      fit: "fill",
      activeName: "second"
    };
  },
  
  created() {
    this.$http
      .post("/apis/ismweb/TestDutyPerson/dutyPersonHistory",{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
      .then(function(res) {
        // this.urlList = res.data.data;
        for (let i of res.data.data) {
          let time=this.$moment(i.realTime).format('YYYY-MM-DD HH:mm:ss');
          let arr = {
            date: time,
            address: i.section,
            name: i.personReal,
            tel: i.tel,
            imgUrl:  i.imgUrl
          };
          this.dutyList.push(arr);
        }
      });
  },
  watch: {
      intervalTime(){
        alert(this.intervalTime)
      }
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.innerbox {
  width: 100%;
  position: relative;
}
.secA {
  display: flex;
  justify-content: space-between;
  height: 397px;
}
hr {
  width: 85%;
  margin: 10px auto 50px;
}
.secA_1,
.secA_2 {
  background: #fff;
  width: 49%;
}
.ml_144 {
  margin-left: 18%;
}
.ml_173 {
  margin-left: 18%;
}
.ml_144,
.ml_173 {
  text-align: left;
  font-family: "MicrosoftYaHei";
  font-size: 18px;
}
.opt_name {
  margin-top: 15px;
}
.opt_phone {
  margin-top: 15px;
}
.opt_name,
.opt_phone {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/*表格部分样式*/
.secB {
  background: #fff;
  margin-top: 31px;
}
</style>


