<template>
  <div class="remote-tour marqueeleft" id="marquee1">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>远程智能巡视</h3>
    </div>
    <div class="bd">
      <!-- 外包围 -->
      <div class="div_scroll">
        <!-- 遍历接口中的图片和站名 -->
        <scroll :data="list" :class-option="defaultOption" class="seamless-warp">
          <ul class="item">
            <li v-for="(item,i) of list" :key="i">
              <a href="#">
                <div class="img">
                  <img :src="`${item.images[0].imgUrl}`" alt />
                </div>
                <div class="span">{{dbm[item.dbm]}}</div>
              </a>
            </li>
          </ul>
        </scroll>
      </div>
    </div>
  </div>
</template>
<script>
// 引入vue中的无缝滚动插件：1.npm install vue-seamless-scroll --save   2.import...  3.components:{scroll}
import scroll from "vue-seamless-scroll";
import { clearInterval } from "timers";
import axios from "axios";
export default {
  name: "remote-tour",
  data() {
    return {
      list: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  components: {
    scroll
  },
  computed: {
    //滚动自定义
    defaultOption() {
      return {
        step: 0.5, //数值越大速度滚动越快
        limitMoveNum: 3, //开始无缝滚动的数据量
        hoverStop: true, //是否开启鼠标悬停stop
        direction: 2, //0向下 1向上 2向左 3向右
        openWatch: true, //开启数据实时监控刷新dom
        singleHeight: 0, //单步运动停止的高度（默认0时无缝不停止的滚动）direction =>0/1
        singleWidth: 0, //单步运动停止的宽度（默认值0是无缝不停止的滚动） direction =>2/3
        waitTime: 0 //单步运动停止的时间（默认值1000ms）
      };
    }
  },
  methods: {
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/remotetour"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  },
  //获取接口数据
  created: function() {
    var self = this;
    // axios.get("http://localhost:3000/remotetour/remotetour").then(function(res) {
    this.$http
      .get(`${this.baseURL}/ismweb/TestRemoteWatch/picGroupList?type=recent`)
      .then(function(res) {
        self.list = res.data.data;
        // console.log(res.data);
      });
  }
};
</script>
<style scoped>
/* 让头部内容和主体内容垂直方向显示 */
.remote-tour {
  display: flex;
  flex-flow: column;
  height: 100%;
  /* border-bottom: 0.25rem solid #1d2027; */
}
a {
  text-decoration: none;
}
.bd {
  list-style: none;
  background: #3f4555;
  flex: 1;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.div_scroll {
  width: 100%;
  overflow: hidden;
}
.seamless-wrap {
  display: inline-block;
  position: relative;
  margin: 0 auto;
}
.bd .item {
  float: left;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  animation: move 20s linear infinite normal;
  animation-fill-mode: forwards;
  margin: 0;
}
.bd li {
  position: relative;
  float: left;
  list-style-type: none;
  margin: 0 0.5rem;
  display: flex;
  flex-flow: column;
}
.bd li a {
  display: flex;
  flex-flow: column;
}
.bd .img img {
  height: 100%;
  width: 100%;
}

.bd .span {
  font-size: 14px;
  font-weight: normal;
  height: 1.7rem;
  line-height: 1.7rem;
  color: white;
  display: block;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (min-width: 1200px) {
  .bd {
    padding: 5% 5%;
    font-size: 1.125rem;
  }
  .bd .img {
    width: 5.25rem;
    height: 5.25rem;
    flex: 1;
    /* padding-bottom: 1rem; */
  }
}
@media screen and (max-width: 1200px) {
  .bd {
    padding: 4% 20%;
    font-size: 1.2rem;
  }
  .bd .img {
    width: 5.25rem;
    height: 5.25rem;
    flex: 1;
    padding-bottom: 1rem;
  }
}
@media screen and (max-width: 768px) {
  .bd {
    padding: 5% 10%;
  }
  .bd li {
    font-size: 1.125rem;
  }
  .bd .img {
    height: 5.25rem;
    width: 5.25rem;
    flex: 1;
    padding-bottom: 1rem;
  }
}
</style>



