<template>
  <div class="innerbox">
    <h3>总报警信息列表</h3>
    <div class="info_content">
      <ul class="bd">
        <li
          v-for="(list,index) of Lists.slice((currentPage-1)*pageSize,(currentPage)*pageSize)"
          :key="index"
        >
          <a href="javascript:;">
            <!-- <div class="infoContent" v-if="list.restoreT===''"> -->
            <div class="infoContent">
              <div :class="`grade${list.alarmLevel} grade mr20`"></div>
              <div class="lineMain">
                <span class="infoDate">{{list.startTime | datefmt('YYYY-MM-DD HH:mm:ss')}}</span>
                <span class="infoTitle">{{list.deviceName}} {{list.summary}}</span>
              </div>
              <div class="infoBody">
                <span>{{list.info}}</span>
                <div v-if="list.isRestore == 0" class="floatRight">未恢复</div>
                <div v-else class="floatRight mr20">
                  <!-- <span
                    v-if="nowDate > list.endTime"
                  >{{ list.endTime | datefmt('YYYY-MM-DD HH:mm:ss')}}</span>
                  {{nowDate > list.endTime ? ' 已恢复' : ' 未恢复'}}-->
                  已恢复
                </div>
                <div v-if="list.isDealState==0" class="floatRight">未处理&nbsp;&nbsp;</div>
                <div v-else class="floatRight">已处理&nbsp;&nbsp;</div>
              </div>
            </div>
          </a>
        </li>
      </ul>
    </div>
    <div class="pagination">
      <!-- 分页器 -->
      <el-pagination
        align="right"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        background
        layout="prev, pager, next"
        :total="Lists.length"
        :page-size="pageSize"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import { stat, close } from "fs";
export default {
  props: ["intervalTime"],
  data() {
    return {
     Lists: [], //报警信息列表
      currentPage: 1, //默认当前页为第1页
      pageSize: 10, //默认显示1条数据
      nowDate: ""
    };
  },

  watch: {
    intervalTime() {
      // alert(this.intervalTime)
      this.getData();
    }
  },
  created() {
    this.handleLists();
    this.getData();
  },
  methods: {
    recover() {},
    noRecover() {},
    handleClick(tab, event) {
      console.log(tab, event);
    },
    handleSizeChange(size) {
      this.currentPage = 1;
      this.pageSize = size;
    },
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
    },

    handleLists() {
      for (var item of this.Lists) {
        // if (item.restoreT === "") {
        this.newLists.push(item);
        // }
      }
      return this.newLists;
    },
    getData() {
      let self = this;
      //时间段获取
      let allTime = self.intervalTime;
      //开始时间
      let startTime = allTime[0];
      // alert(startTime)
      //结束时间
      let endTime = allTime[1];
      // alert(endTime)
      let parama = {
        dataAllBool: true,
        startTime: startTime,
        endTime: endTime
      };
      // console.log(parama)
      self.$http
        .post("/apis/ismweb/AlarmRecord/alarmListNew", parama)
        .then(function(res) {
          self.Lists = res.data.data;
          console.log(self.Lists);
          // alert(res);
        })
        .catch(function(error) {
          console.log("错误问题是" + error);
        });
    }
  },
  computed: {
    recoveryTime: function(t) {
      if (t > this.nowDate) {
        var time = t | datefmt("YYYY-MM-DD HH:mm:ss");
        return time;
      }
      // return this.isRecovery ? '已恢复' : '未恢复'
    }
  }
};
</script>
<style scoped>
* {
  margin: 0 0 6px 0;
  padding: 0;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
/*信息列表的样式*/
.bd {
  padding: 0 30px;
  flex: 1;
  overflow: hidden;
  list-style: none;
  background: #fff;
  text-align: left;
}
.bd > li {
  height: 75px;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.bd > li > a {
  text-decoration: none;
}
.bd > li:hover {
  background: #f3fff2;
}
.infoContent {
  padding: 10px 30px 25px 30px;
  overflow: hidden;
  width: 100%;
}
.infoDate {
  /* font-weight: 600; */
  color: #4b97f5;
  font-family: "MicrosoftYaHei";
}
.infoTitle {
  margin-left: 90px;
  color: #0d0d0d;
}
.infoBody {
  font-size: 16px;
}
.infoBody > span {
  display: inline-block;
  width: 50%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #707070;
}
.el-pagination {
  margin: 20px auto;
}
/*右侧信息*/
.grade {
  height: 26px;
  width: 50px;
  float: right;
}
.grade1 {
  background: url(../../assets/grade1.png) no-repeat left center;
}
.grade2 {
  background: url(../../assets/grade2.png) no-repeat left center;
}
.grade3 {
  background: url(../../assets/grade3.png) no-repeat left center;
}
.grade4 {
  background: url(../../assets/grade4.png) no-repeat left center;
}
.floatRight {
  float: right;
}
.mr20 {
  /* margin-right: 20px; */
}
.infoBody > div {
  color: #929292;
}
</style>





