<template>
  <div id="main" class="container-fluid">
    <div class="row">
      <!-- 使用bootstrap中的列排序来调整一行内容排列的先后顺序，col-lg-push-2表示整体右移了两份，col-lg-pull-6表示整体左移了6份 -->
      <!-- 屏幕>1200px：“左边”占2份   “中间”占4份，左偏移2份   “右边”占3份，左偏移1份 -->
      <!-- 1200px>屏幕>992px: “中间”占8份，左偏移2份  左边和右边各占6份  变成了两行-->
      <div
        class="headerCenter col-lg-4 col-lg-offset-2 col-lg-push-2 col-md-8 col-md-offset-2 col-sm-8 col-sm-offset-2 col-xs-10 col-xs-offset-1"
      >广电集团电务大数据分析</div>
      <div class="headerLeft col-lg-2 col-lg-pull-6 col-md-6 col-sm-6 col-xs-12">
        广州站&nbsp;&nbsp;
        <img src="../assets/bottom_link.png" alt="选站" />
      </div>
      <div class="headerRight col-lg-3 col-lg-offset-1 col-md-6 col-sm-6 col-xs-12">{{timer}}<br/> {{day}}</div>
    </div>
  </div>
</template>
<script >
import { setInterval } from "timers";
export default {
  name: "commenheader",
  data() {
    return {
      timer: "",
      day: ''
    };
  },
  created: function() {
    var time = new Date();
    var date = time.toLocaleDateString();
    var h = time.getHours();
    var m = time.getMinutes();
    var s = time.getSeconds();
    if (h < 10) {
      h = "0" + h;
    }
    if (m < 10) {
      m = "0" + m;
    }
    if (s < 10) {
      s = "0" + s;
    }
    this.day = `${date}  `;
    this.timer = `${h}:${m}:${s}`;

    setInterval(() => {
      // alert(1)
      //alert(this.timer);
      var time = new Date();
      var date = time.toLocaleDateString();
      var h = time.getHours();
      var m = time.getMinutes();
      var s = time.getSeconds();
      if (h < 10) {
        h = "0" + h;
      }
      if (m < 10) {
        m = "0" + m;
      }
      if (s < 10) {
        s = "0" + s;
      }
      this.day = `${date}  `;
      this.timer = `${h}:${m}:${s}`;
    }, 1000);
  }
};
</script>
<style >
#main {
  width: 100%;
  max-width: 100%;
  background: #353b4d;
  color: #fff;
  line-height: 3.5rem;
  text-align: center;
  font-weight: normal;
  border-bottom: 0.25rem solid #1d2027;
}
.headerLeft {
  line-height: 3.5rem;
  /* padding-left: 4rem; */
  font-size: 1.17rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.headerCenter {
  margin: 0 auto;
  height: 100%;
  /* border-bottom: 4rem solid #1D2027; */
  font-size: 1.5rem;
  /* border-right: 4rem solid #353B4D;   */
  /* border-left: 4rem solid #353B4D;   */
  /* overflow: hidden; */
  background: url("../assets/top_bg.png");
  background-size: 100% 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.headerRight {
  /* line-height: 4rem; */
  font-size: 1.17rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.headerLeft > span {
  margin-left: 0.3rem;
  height: 1.5rem;
  line-height: 1.5rem;
}
@media only screen and (min-width: 992px) {
  .row {
    height: 100%;
  }
  /* #main{
        height: 5.981%;
    } */
  .headerLeft {
    order: 1;
    height: 100%;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    padding-left: 45px;
  }
  .headerCenter {
    order: 2;
  }
  .headerRight {
    order: 3;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 45px;
    padding-top:8px;
    line-height: 20px ;
    font-size: 16px;
  }
}
/* 当屏幕小于992px，大于768px时 */
@media screen and (min-width: 768px) and (max-width: 992px) {
  .headerLeft {
    text-align: left;
    padding-left: 18%;
  }
  .headerRight {
    text-align: right;
    padding-right: 18%;
  }
  .headerLeft,
  .headerRight {
    font-size: 19px;
    height: 55px;
    line-height: 55px;
  }
  .headerCenter {
    font-size: 23px;
    height: 55px;
    line-height: 55px;
  }
}
/* 当屏幕小于768px时 */
@media screen and (max-width: 768px) {
  .headerLeft,
  .headerRight {
    font-size: 18px;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  .headerCenter {
    font-size: 22px;
    height: 45px;
    line-height: 45px;
  }
}
</style>



