<template>
  <el-container>
    <el-header>远程智能巡视</el-header>
    <el-container>
      <el-aside width="20%">
        <aside :class="collapsed?'menu-collapsed':'menu-expanded'">
          <!--导航菜单-->

          <ul
            role="menu"
            class="el-menu el-menu--inline"
            data-old-padding-top
            data-old-padding-bottom
            data-old-overflow
            style="background-color: rgb(84, 92, 100);"
          >
            <!-- <li
              data-v-242a32d0
              role="menuitem"
              tabindex="-1"
              class="el-menu-item"
              style=" color: rgb(255, 255, 255); background-color: rgb(84, 92, 100);"
            >
              <router-link to="/">
                <img class="home_icon" src="../../assets/home.png"> 首页
              </router-link>
            </li>-->
          </ul>

          <el-menu
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            v-bind:style=" collapsed ? {} : { 'width': '' } "
            :default-active="$route.path"
            class="el-menu-vertical-demo"
            unique-opened
            router
            v-show="!collapsed"
          >
            <template v-for="(item,index) of menuData">
              <el-submenu :index="index+''" :key="index">
                <template slot="title">
                  <i :class="item.iconCls"></i>
                  {{item.name}}
                </template>
                <el-menu-item
                  v-for="child of item.children"
                  :index="child.path"
                  :key="child.path"
                >
                <div style="border-bottom: 1px solid #4f566a;">
                    {{child.name}}
                </div>
                </el-menu-item>
              </el-submenu>
              <el-menu-item
                v-if="item.leaf&&item.children.length>0"
                :index="item.children[0].path"
                :key="item"
              >
                <i :class="item.iconCls"></i>
                {{item.children[0].name}}
              </el-menu-item>
            </template>
          </el-menu>
          <!-- 导航菜单-折叠后-->
        </aside>
      </el-aside>
      <div class="80%" style="background: #f6f6f6;display:flex;flex-flow: column;">
        <div class="time_select">
          <span class="station">站台 :&nbsp;&nbsp;&nbsp;</span>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item of photoList"
              :key="item.station"
              :label="item.label"
              :value="item.station"
            ></el-option>
          </el-select>
          <div class="time_end">
            <span>时间 :&nbsp;&nbsp;&nbsp;</span>
            <el-date-picker
              v-model="value1"
              align="right"
              type="date"
              placeholder="选择日期"
              :picker-options="pickerOptions"
            ></el-date-picker>
          </div>
        </div>
        <el-main>
          <transition name="fade" mode="out-in">
            <router-view></router-view>
          </transition>
        </el-main>
      </div>
    </el-container>
  </el-container>
</template>
<style scoped>
.el-menu:last-child .el-menu-item {
  color: #fff !important;
  background: #4b97f5 !important;
}

.el-menu {
  border: none !important;
}
.w100 {
  width: 100%;
  /* background: #fff; */
  max-width: 80.75%;
  /* max-height:92.25%; */
  background: rgb(246, 245, 248);
}
.time_select {
  background: #fff;
}
/* .el-tabs__nav-wrap ::after {
  height: 0 !important;
} */
.time_select {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 70px;
  line-height: 70px;
  font-size: 18px;
  display: flex;
}
.station,
.time_end,
.month_select {
  /* padding-top:10px; */
  margin-left: 20px;
  padding-left: 1.3rem;
}
.el-header,
.el-footer {
  background-color: #353b4d;
  color: #ffffff;
  text-align: left;
  line-height: 70px;
  font-size: 22px;
  height: 70px !important;
  padding: 0 20px 0 50px !important;
}
.home_icon {
  width: 25px;
  margin-bottom: 8px;
}
.el-aside {
  background-color: #3f4555;
  color: #fff;
  text-align: center;
  line-height: 200px;
  padding-left: 3px;
}

.el-main {
  background-color: #f6f6f6;
  color: #333;
  text-align: center;
  /* line-height: 160px; */
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
/*站台下拉菜单样式*/
.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
</style>

<script>
let data = () => {
  return {
    photoList: [
      {
        station: "乌鲁木齐站",
        phone: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo1.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png"),
            require("../../assets/photo6.png")
          ]
        }
      },
      {
        station: "广州站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo2.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png")
          ]
        }
      },
      {
        station: "怀化站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo3.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png"),
            require("../../assets/photo6.png"),
            require("../../assets/photo6.png")
          ]
        }
      },
      {
        station: "湘潭站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo1.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png")
          ]
        }
      },
      {
        station: "岳阳站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo2.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png"),
            require("../../assets/photo6.png")
          ]
        }
      },
      {
        station: "暮云站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo3.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png")
          ]
        }
      },
      {
        station: "三门站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo1.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png"),
            require("../../assets/photo6.png")
          ]
        }
      },
      {
        station: "株洲站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo2.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png"),
            require("../../assets/photo6.png"),
            require("../../assets/photo6.png"),
            require("../../assets/photo6.png")
          ]
        }
      },
      {
        station: "佛山站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo3.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png")
          ]
        }
      },
      {
        station: "邵东站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo1.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png")
          ]
        }
      },
      {
        station: "衡阳站",
        photo: {
          real_time: "2019-05-20 18:30:25",
          image: [
            require("../../assets/photo2.png"),
            require("../../assets/photo2.png"),
            require("../../assets/photo3.png"),
            require("../../assets/photo4.png"),
            require("../../assets/photo5.png")
          ]
        }
      }
    ],
    sysName: "后台管理系统",
    menuData: [],
    collapsed: false,
    sysUserName: "管理员",
    activeName: "second",
    // tabPosition: 'left',
    pickerOptions: {
      disabledDate(time) {
        return time.getTime() > Date.now();
      },
      shortcuts: [
        {
          text: "今天",
          onClick(picker) {
            picker.$emit("pick", new Date());
          }
        },
        {
          text: "昨天",
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit("pick", date);
          }
        },
        {
          text: "一周前",
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit("pick", date);
          }
        }
      ]
    },
    value: "",
    value1: ""
  };
};

let initMenu = function() {
  for (let i in this.$router.options.routes_RemoteTour) {
    let root = this.$router.options.routes_RemoteTour[i];
    if (root.hidden) continue;
    let children = [];
    for (let j in root.children) {
      let item = root.children[j];
      if (item.hidden) continue;
      children.push(item);
    }

    if (children.length < 1) continue;
    this.menuData.push(root);
    root.children = children;
  }
};

export default {
  name: "home",
  data: data,
  methods: {
    initMenu: initMenu,
    //折叠导航栏
    collapse: function() {
      this.collapsed = !this.collapsed;
    },
    showMenu: function(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
        "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    },
    handleClick() {
      alert("button click");
    }
  },
  mounted: function() {
    this.initMenu();
  }
};
</script>


