<template>
  <div>
    <div class="innerbox">
      <h3>各站设备类报警情况</h3>
      <div class="tablePd" style>
        <el-table
          :data="tableData"
          stripe
          height="667"
          style="width: 100%; font-size:16px; "
          :header-row-style="{height:'3.44rem',color:'#0d0d0d',background:'#F5F7FA',fontSize:'18px'}"
          :row-style="{height:'3.44rem'}"
          :header-cell-style="getRowClass"
          :default-sort="{prop: 'total', order: 'descending'}"
        >
          <el-table-column
            prop="stationName"
            label="车站名"
            align="center"
            width="110"
            height="3.44rem"
            style="{'font-size':'14px'}"
          ></el-table-column>
          <el-table-column label="道岔" align="center">
            <el-table-column
              prop="countDaocha1"
              label="1级"
              align="center"
              cell-style="font-size: 30px"
            ></el-table-column>
            <el-table-column prop="countDaocha2" label="2级" align="center"></el-table-column>
            <el-table-column prop="countDaocha3" label="3级" align="center"></el-table-column>
            <el-table-column prop="countDaocha4" label="其他" align="center"></el-table-column>
          </el-table-column>
          <el-table-column label="信号机" align="center">
            <el-table-column prop="countXHJ1" label="1级" align="center"></el-table-column>
            <el-table-column prop="countXHJ2" label="2级" align="center"></el-table-column>
            <el-table-column prop="countXHJ3" label="3级" align="center"></el-table-column>
            <el-table-column prop="countXHJ4" label="其他" align="center"></el-table-column>
          </el-table-column>
          <el-table-column label="轨道电路" align="center">
            <el-table-column prop="countGDDL1" label="1级" align="center"></el-table-column>
            <el-table-column prop="countGDDL2" label="2级" align="center"></el-table-column>
            <el-table-column prop="countGDDL3" label="3级" align="center"></el-table-column>
            <el-table-column prop="countGDDL4" label="其他" align="center"></el-table-column>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="innerbox  mtp125">
      <h3>全段设备类型故障</h3>
      <div class="myChartBox">
        <div class="txta">当天故障比例</div>
        <div class="txtb">本周故障比例</div>
        <div class="txtc">本月故障比例</div>
        <div id="myChart"></div>
        <div class="texBox texd">
          <p> <span>◆</span>整体情况：道岔类故障比例偏高</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// 引入基本模板
let echarts = require("echarts/lib/echarts");
// 引入柱状图组件
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/chart/pie");
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/graphic");
require("echarts/lib/component/legend");
require("echarts/lib/component/title");
export default {
  props: ["intervalTime"],
  data() {
    return {
      tableData: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  watch: {
      intervalTime(){
        // alert(this.intervalTime)
        // this.getData();
      }
  },
  created() {
    
  },
  mounted() {
    this.drawLine();
    this.getData();
  },
  methods: {
    drawLine() {
      this.$http
        .get("/apis/ismweb/AlarmCacl/alarmCountListByYear")
        .then(function(res) {
          var resData = res.data.data;
          //  let dachaData=[resData.listDay.daochaCount,resData.listWeek.daochaCount,resData.listMonth.daochaCount];
          //  let xhjData=[resData.listDay.xhjCount,resData.listWeek.xhjCount,resData.listMonth.xhjCount];
          //  let gddlData=[resData.listDay.TrackDevCount,resData.listWeek.TrackDevCount,resData.listMonth.TrackDevCount];
          //  let restsData=[resData.listDay.restsCount,resData.listWeek.restsCount,resData.listMonth.restsCount];
          // 基于准备好的dom，初始化echarts实例
          let myChart = echarts.init(document.getElementById("myChart"));
          var option = {
            legend: { orient: "vertical", x: "3%" },
            tooltip: {},
            title: {
              text: "整体情况：全段一、二级报警比例偏高",
              x: "center",
              y: "bottom",
              show: false
            },
            dataset: {
              source: [
                ["product", "当天报警比例", "本周报警比例", "本月报警比例"],
                [
                  "道岔类",
                  resData.listDay.daochaCount,
                  resData.listWeek.daochaCount,
                  resData.listMonth.daochaCount
                ],
                [
                  "信号机类",
                  resData.listDay.xhjCount,
                  resData.listWeek.xhjCount,
                  resData.listMonth.xhjCount
                ],
                [
                  "轨道电路",
                  resData.listDay.TrackDevCount,
                  resData.listWeek.TrackDevCount,
                  resData.listMonth.TrackDevCount
                ],
                [
                  "其他",
                  resData.listDay.restsCount,
                  resData.listWeek.restsCount,
                  resData.listMonth.restsCount
                ]
              ]
            },
            color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
            graphic: {
              type: "text",
              left: "center",
              top: "40%",
              style: {
                text: "",
                textAlign: "center",
                fill: "#333",
                fontSize: 20,
                fontWeight: 700
              }
            },
            series: [
              {
                type: "pie",
                name: "当天报警比例",
                title: {
                  text: "当天报警比例"
                },
                radius: "60%",
                center: ["20%", "50%"],
                itemStyle: {
                  normal: {
                    label: {
                      show: true,
                      formatter: "{d}%"
                    },
                    labelLine: { show: true }
                  }
                }
              },
              {
                type: "pie",
                name: "本周报警比例",
                radius: "60%",
                center: ["50%", "50%"],
                encode: {
                  itemName: "product",
                  value: "本周报警比例"
                }
              },
              {
                type: "pie",
                name: "本月报警比例",
                radius: "60%",
                center: ["80%", "50%"],
                encode: {
                  itemName: "product",
                  value: "本月报警比例"
                }
              }
            ]
          };
          myChart.setOption(option);
          $(window).resize(function() {
            myChart.resize();
          });
        });
    },
    // 给表单的表头添加背景颜色
    getRowClass({ row, column, rowIndex, columnIndex }) {
      if (
        (rowIndex === 1 && columnIndex === 0) ||
        columnIndex === 4 ||
        columnIndex === 8
      ) {
        return "background: url(/static/images/bgcircularred.png)no-repeat center ;color:#fff;font-weight: normal;font-size:12px;";
      } else if (
        (rowIndex === 1 && columnIndex === 1) ||
        columnIndex === 5 ||
        columnIndex === 9
      ) {
        return "background: url(/static/images/bgcircularblue.png)no-repeat center ;color:#fff;font-weight: normal;font-size:12px;";
      } else if (
        (rowIndex === 1 && columnIndex === 2) ||
        columnIndex === 6 ||
        columnIndex === 10
      ) {
        return "background: url(/static/images/bgcircularyellow.png)no-repeat center ;color:#fff;font-weight: normal;font-size:12px; ";
      }
      if (
        (rowIndex === 1 && columnIndex === 3) ||
        columnIndex === 7 ||
        columnIndex === 11
      ) {
        return "background: url(/static/images/bgcirculargray.png)no-repeat center ;color:#fff;font-weight: normal;font-size:12px;";
      } else {
        return "";
      }
    },
    getData(){
      // alert(this.intervalTime[0]);
      // alert(this.intervalTime[1]);
       this.$http
      .post("/apis/ismweb/AlarmCacl/alarmLevelCountAllBy3dev",{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
      .then(function(res) {
        let resdata = res.data.data;
        // console.log(resdata);
        // let tableData=[];
        // let name=resdata.data.data;
        this.tableData=[];
        for (let item of resdata) {
          let stationName = this.dbm[item.dbm];
          //道岔报警数
          let countDaocha1 = item.alarmDaoCha1;
          let countDaocha2 = item.alarmDaoCha2;
          let countDaocha3 = item.alarmDaoCha3;
          let countDaocha4 = item.alarmDaoCha4;
          //信号机报警数
          let countXHJ1 = item.alarmXhj1;
          let countXHJ2 = item.alarmXhj2;
          let countXHJ3 = item.alarmXhj3;
          let countXHJ4 = item.alarmXhj4;
          //轨道电路报警数
          let countGDDL1 = item.alarmTrack1;
          let countGDDL2 = item.alarmTrack2;
          let countGDDL3 = item.alarmTrack3;
          let countGDDL4 = item.alarmTrack4;

          let arr = {
            stationName: stationName,
            // countDaocha1: countDaocha1,
            //道岔报警数
            countDaocha1: item.alarmDaoCha1,
            countDaocha2: item.alarmDaoCha2,
            countDaocha3: item.alarmDaoCha3,
            countDaocha4: item.alarmDaoCha4,
            //信号机报警数
            countXHJ1: item.alarmXhj1,
            countXHJ2: item.alarmXhj2,
            countXHJ3: item.alarmXhj3,
            countXHJ4: item.alarmXhj4,
            //轨道电路报警数
            countGDDL1: item.alarmTrack1,
            countGDDL2: item.alarmTrack2,
            countGDDL3: item.alarmTrack3,
            countGDDL4: item.alarmTrack4
          };
          this.tableData.push(arr);
        }
      });
    }
  }
};
</script>
<style scoped>
.el-table--border th {
  border-right: 1px solid #ddd !important;
  border-bottom: 1px solid #ddd;
}
.mtp125 {
  margin-top: 1.25rem;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.el-table__body tr,
.el-table__body td {
  padding: 0;
  height: 3.44rem;
}
#myChart {
  height: 23rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
  position: relative;
}
#myChart2 {
  height: 32rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
  margin-top: -30px;
  overflow-x: hidden;
}
.tablePd {
  padding: 0 3.81rem 2.44rem 3.81rem;
}
.tablePd span {
  color: #fff;
  display: inline-table;
  background-color: #8eb9f5;
  width: 21px;
  height: 21px;
  overflow: hidden;
}
.color1 {
  background-color: #f54545 !important;
}
.color2 {
  background-color: #ff8547 !important;
}
.color3 {
  background-color: #ffac38 !important;
}
.red {
  color: #fe4c4c !important;
  background: none !important;
}
.texBox {
  /* width: 50%; */
  margin: 15px auto 0 auto;
  padding-bottom: 15px;
  text-align: center;
}
.texBox p {
   padding-left: 32px;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.texBox span {
  color: #4B97F5;
  display:inline-block;
  font-size: 30px;
  margin-right:10px; 
}
.myChartBox {
  position: relative;
  height: 400px;
}
.txta {
  position: absolute;
  left: 20%;
  transform: translateX(-50%);
}
.txtb {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.txtc {
  position: absolute;
  left: 80%;
  transform: translateX(-50%);
}
.texd {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0px;
}
</style>



