<template>
  <div class="alarm-ratio">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>各级报警比例</h3>
    </div>
    <div class="bd">
      <div id="myChart_ratio"></div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
var echarts = require("echarts");
export default {
  data() {
    return {
      lists: [],
      data: []
    };
  },
  methods: {
    //为了做成响应式，设置echarts字体大小的方法
    setFontSize(size) {
      var windowWidth = document.body.clientWidth;
      if (windowWidth > 1024 && windowWidth < 4000) {
        return (size * windowWidth) / 1920;
      } else if (windowWidth <= 800) {
        return (size * windowWidth) / 500;
      } else if (windowWidth < 1024) {
        return (size * windowWidth) / 800;
      } else if (windowWidth == 1024) {
        return (size * windowWidth) / 1366;
      } else if (windowWidth >= 4000) {
        return (size * windowWidth) / 1600;
      }
    },
    //echarts饼图
    drawPie() {
      // axios.get("http://localhost:3000/alarm/alarmRatio").then(function(res) {
      this.$http.get(`${this.baseURL}/ismweb/AlarmCacl/alarmRateByLevel`).then(function(res) {
        //console.log(res.data.data[0][0].count+'这是什么几把玩意儿');
        // alert(res.data.data[0][0].count);
        let lists=[];
        let newData=res.data.data.listWeek;
        // console.log(res)

        // let alarmArry={1:'一级',2:'二级',3:'三级',4: '预警'};
        // console.log(res.data.listWeek)

        // for(let i of res.data.data.listWeek){
        //   // alert(i.cnt);
        //     let itemArry={
        //       value: i.cnt, name: alarmArry[i.alarmLevel]
        //     }
        //     lists.push(itemArry);
        // }
        lists=[
          {value:newData.alarmLevel1,name: '一级'},
          {value:newData.alarmLevel2,name: '二级'},
          {value:newData.alarmLevel3,name: '三级'},
          {value:newData.alarmLevel4,name: '预警'},
        ]
        // console.log(lists);

        var myChart_ratio = echarts.init(
          document.getElementById("myChart_ratio")
        );

        var option = {
          color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
          tooltip: {
            trigger: "item",
            show: true,
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            x: "center",
            y: "80%",
            data: ["一级", "二级", "三级", "预警"],
            textStyle: {
              color: "#fff",
              fontSize: 12
            }
          },
          toolbox: {
            show: false,
            feature: {
              mark: { show: true },
              dataView: { show: true, readOnly: false },
              magicType: {
                show: true,
                type: ["line", "bar", "stack", "tiled"]
              },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          series: [
            {
              name: "报警比例",
              type: "pie",
              radius: "55%",
              center: ["50%", "40%"],
              data: lists,
              // [
              //   { value: res.data.data[0][0].count, name: "一级" },
              //   { value: res.data.data[0][3].count, name: "预警" },
              //   { value: res.data.data[0][2].count, name: "三级" },
              //   { value: res.data.data[0][1].count, name: "二级" }
              // ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                },
                normal: {
                  label: {
                    show: true,
                    formatter: "{b}",
                    fontSize: 14
                  },
                  labelLine: { show: true }
                }
              },
              label: {
                normal: {
                  show: true
                },
                emphasis: {
                  show: true
                }
              }
            }
          ]
        };
        myChart_ratio.setOption(option);
        //让echarts图表根据屏幕的尺寸来响应大小
        // window.onresize = myChart_ratio.resize;
        $(window).resize(function() {
          myChart_ratio.resize();
        });
      });
    },
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/alarm/alarmRatio"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  },
  mounted() {
    this.drawPie();
  }
};
</script>
<style scoped>
.alarm-ratio {
  width: 100%;
  height: 100%;
  background: #3f4555;
  border-bottom: 0.5rem solid #1d2027;
}
.bd {
  flex: 1;
}
#myChart_ratio {
  height: 300px;
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 1200px) {
  #myChart_ratio {
    height: 250px;
    margin-top: 10px;
  }
}
</style>



