<template>
  <div>
    <div class="innerbox">
      <h3>报警车站次数排行</h3>
      <div class="tablePd" style>
        <el-table
          :data="tableData"
          stripe
          height=""
          style="width: 100%; font-size:16px; "
          :header-row-style="{height:'3.44rem'}"
          :row-style="{height:'3.44rem'}"
          :header-cell-style="{background:'#F5F7FA',color:'#0d0d0d','font-size':'18px'}"
          :default-sort="{prop: 'total', order: 'descending'}"
        >
          <el-table-column prop="ranking" label="排名" align="center" width="110" height="3.44rem">
            <template slot-scope="scope">
              <span v-if=" scope.row.ranking == '1'" class="color1">{{ scope.row.ranking }}</span>
              <span v-else-if=" scope.row.ranking == '2'" class="color2">{{ scope.row.ranking }}</span>
              <span v-else-if=" scope.row.ranking == '3'" class="color3">{{ scope.row.ranking }}</span>
              <span v-else>{{ scope.row.ranking }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="dbm" label="车站名" align="center"></el-table-column>
          <el-table-column prop="alarmCount" label="总报警数" align="center"></el-table-column>
          <el-table-column
            prop="oneAlarm"
            label="一级报警"
            align="center"
            :sortable="true"
            :sort-method="sortByLevel1"
          >
            <template slot-scope="scope">
              <span class="red">{{ scope.row.oneAlarm }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="twoAlarm"
            label="二级报警"
            align="center"
            :sortable="true"
            :sort-method="sortByLevel2"
          ></el-table-column>
          <el-table-column
            prop="threeAlarm"
            label="三级报警"
            align="center"
            :sortable="true"
            :sort-method="sortByLevel3"
          ></el-table-column>
          <el-table-column
            prop="weeklyRingRatio"
            label="周环比"
            align="center"
            :sortable="true"
            :sort-method="sortByWeeklyRingRatio"
          ></el-table-column>
          <el-table-column
            prop="monthlyRingRatio"
            label="月环比"
            align="center"
            :sortable="true"
            :sort-method="sortByMonthlyRingRatio"
          ></el-table-column>
        </el-table>
      </div>
    </div>
    <div class="innerbox mtp125">
      <h3>全段报警变化趋势</h3>
      <div id="myChart"></div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { close } from "fs";
// 引入基本模板
let echarts = require("echarts/lib/echarts");
// 引入柱状图组件
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/grid");
require("echarts/lib/component/legend");

export default {
  props: ["intervalTime"],
  data() {
    return {
      tableData: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  mounted() {
    this.drawLine();
  },
  watch: {
      intervalTime(){
        this.getTableData();
      }
  },
  created() {
    this.getTableData();
  },
  methods: {
    drawLine() {
      var self = this;
      self.$http
        .post("/apis/ismweb/AlarmCacl/alarmCountListByMonth",{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
        .then(function(res) {
          let monthdata = [];
          let level1data = [];
          let level2data = [];
          let level3data = [];
          let level4data = [];

          for (let item of res.data.data) {
            monthdata.push(item.month+'月');
            level1data.push(item.countLevel1);
            level2data.push(item.countLevel2);
            level3data.push(item.countLevel3);
            level4data.push(item.countLevel4);
          }

          // 基于准备好的dom，初始化echarts实例
          let myChart = echarts.init(document.getElementById("myChart"));
          // 绘制图表
          myChart.setOption({
            title: {
              text: ""
            },
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross",
                label: {
                  backgroundColor: "#6a7985"
                }
              }
            },
            legend: {
              data: ["一级", "二级", "三级", "预警"],
              textStyle: {
                fontSize: 16
              }
            },
            toolbox: {
              feature: {
                saveAsImage: {}
              }
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            xAxis: [
              {
                type: "category",
                boundaryGap: false,
                name:'2019年',
                axisLabel: {
                  fontSize: 12
                },
                data: monthdata,
                axisLabel: {
                  show: true,
                  textStyle: {
                    // color: 'red',
                    fontSize: 16
                  }
                }
                // textStyle: {
                //     "fontSize": 18
                // }
              }
            ],
            yAxis: [
              {
                type: "value",
                name:'次'
              }
            ],
            series: [
              {
                name: "一级",
                type: "line",
                stack: "总量",
                areaStyle: { color: "red" },
                // data: [2, 3, 4, 3, 5, 4, 5, 4, 3, 5, 4, 5],
                data: level1data,
                color: "red"
              },
              {
                name: "二级",
                type: "line",
                stack: "总量",
                areaStyle: { color: "#FAD34C" },
                // data: [12, 13, 14, 14, 17, 16, 15, 14, 14, 17, 16, 15],
                data: level2data,
                color: "#FAD34C"
              },
              {
                name: "三级",
                type: "line",
                stack: "总量",
                areaStyle: { color: "#4B97F5" },
                // data: [20, 22, 21, 24, 20, 20, 24, 21, 24, 20, 20, 24],
                data: level3data,
                color: "#4B97F5"
              },
              {
                name: "预警",
                type: "line",
                stack: "总量",
                areaStyle: { normal: {}, color: "#D2D2D2" },
                // data: [32, 33, 30, 33, 39, 33, 32, 30, 33, 39, 33, 32],
                data: level4data,
                color: "#D2D2D2"
              }
            ]
          });
          $(window).resize(function() {
            myChart.resize();
            // myChart2.resize();
          });
        });
    },
    sortByLevel1(obj1, obj2) {
      let val1 = obj1.oneAlarm;
      let val2 = obj2.oneAlarm;
      return val1 - val2;
    },
    sortByLevel2(obj1, obj2) {
      let val1 = obj1.twoAlarm;
      let val2 = obj2.twoAlarm;
      return val1 - val2;
    },
    sortByLevel3(obj1, obj2) {
      let val1 = obj1.threeAlarm;
      let val2 = obj2.threeAlarm;
      return val1 - val2;
    },
    sortByMonthlyRingRatio(obj1, obj2) {
      let val1 = obj1.MonthlyRingRatio;
      let val2 = obj2.MonthlyRingRatio;
      return val1 - val2;
    },
    sortByWeeklyRingRatio(obj1, obj2) {
      let val1 = obj1.weeklyRingRatio;
      let val2 = obj2.weeklyRingRatio;
      return val1 - val2;
    },
    indexMethod(index) {
      return index * 1;
    },
    getTableData(){
    var self = this;
    //获取报警车站次数排行数据
    self.$http
      .get("/apis/ismweb/AlarmCacl/alarmRateListByLevel")
      .then(function(res) {
        self.tableData=[];
        let table = res.data.data.sort((a, b) => b.alarmCount - a.alarmCount);
        let j = 0;
        for (let i of table) {
          j++;
          let weeklyRingRatio = (
            ((i.countAlarmAll - i.countLastWeekCount) / i.countLastWeekCount) *
            100
          ).toFixed(2);
          let monthlyRingRatio = (
            ((i.countMonth - i.countLastMonth) /
              i.countLastMonth) *
            100
          ).toFixed(2);
          let arr = {
            ranking: j,
            dbm: self.dbm[i.dbm],
            oneAlarm: i.countAlarm1,
            twoAlarm: i.countAlarm2,
            threeAlarm: i.countAlarm3,
            alarmCount: i.countAlarmAll,
            weeklyRingRatio: weeklyRingRatio,
            monthlyRingRatio: monthlyRingRatio
          };
          self.tableData.push(arr);
        }
      });
    }
  }
};
</script>
<style scoped>
.mtp125 {
  margin-top: 1.25rem;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.el-table__body tr,
.el-table__body td {
  padding: 0;
  height: 3.44rem;
}
#myChart {
  height: 19.6rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
}
.tablePd {
  padding: 0 3.81rem 2.44rem 3.81rem;
}
.tablePd span {
  color: #fff;
  display: inline-table;
  background-color: #8eb9f5;
  width: 21px;
  height: 21px;
  overflow: hidden;
}
.color1 {
  background-color: #f54545 !important;
}
.color2 {
  background-color: #ff8547 !important;
}
.color3 {
  background-color: #ffac38 !important;
}
.red {
  color: #fe4c4c !important;
  background: none !important;
}
</style>



