<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <!-- <BlogHeader></BlogHeader> -->
    <!-- <CommonHeader></CommonHeader> -->
    <router-view />
  </div>
</template>

<script>
import BlogHeader from "./components/Blog-header";
// import CommonHeader from './components/CommonHeader'
export default {
  name: "App",
  components: {
    BlogHeader
    // ,CommonHeader
  }
};
</script>

<style>
/* aside{
  width: 100% ;
} */
* {
  margin: 0;
  padding: 0;
}
#app {
  height: 100%;
  display: flex;
  flex-flow: column;
}
html,
body {
  height: 100%;
  /* width: 3000px; */
  /* overflow: hidden; */
}
body {
  background: #1d2027;
}
ul {
  margin: 0;
}
.bg_none {
  background: none !important;
}
.wp20 {
  width: 20% !important;
}
.wp80 {
  width: 80% !important;
  display: flex;
  flex-flow: column;
}
.hd {
  height: 2.5rem;
  background: #353b4d;
  overflow: hidden;
}
.hd h3 {
  color: white;
  height: 1.2rem;
  line-height: 1.2rem;
  font-size: 17px;
  position: relative;
  padding-left: 1.3rem;
  margin: 0.6rem 4rem 0.6rem 1.2rem;
  display: inline-block;
}
.hd h3:after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 0.18rem;
}
.hd a {
  display: block;
  text-decoration: none;
  height: 100%;
  width: 20px;
  background: url(./assets/abg1.png) no-repeat center center;
  background-size: 80%;
}
.hd a:hover {
  background: url(./assets/abg2.png) no-repeat center center;
}
.more {
  float: right;
  color: white;
  line-height: 42px;
  height: 42px;
  letter-spacing: -6px;
  margin-right: 10px;
}
.bd {
  background: #3f4555;
  overflow: hidden;
  color: #fff;
}
.h11_6rem {
  height: 11.6rem;
}

/* 二级子页面样式 */
.el-menu {
  border: none !important;
}
.el-submenu__title {
  display: none;
}
.el-menu-item:hover {
  background: rgba(75, 151, 245, 0.5) !important;
  color: #fff !important;
}
.el-menu-item:hover a {
  color: #fff !important;
}
.el-menu-item,
.el-menu {
  background: none !important;
  color: #bbcbfe !important;
}
/* .is-active {
  background: #4b97f5 !important;
  color: #fff !important;
} */

.el-menu-item.is-active {
  color: #fff !important;
  background: #4b97f5 !important;
}
.el-menu-item {
  height: 70px !important;
  line-height: 70px !important;
  font-size: 20px !important;
}
.el-menu-item a {
  text-decoration: none;
  color: #bbcbfe !important;
  display: block;
}
/* .el-menu-item a:hover{
  color: #fff;
} */
.el-menu-item {
  text-align: left;
  /* border-bottom: 1px solid #4f566a; */
}
.el-icon-s-home {
  width: 40px !important;
  height: 34px !important;
  padding: 0;
  font-size: 25px;
  color: #f4f4f4;
}
.el-tabs__item {
  font-size: 18px !important;
}

.el-table th > .cell {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: bold;
}

.el-header,
.el-footer {
  background-color: #353b4d;
  color: #ffffff;
  text-align: left;
  line-height: 70px;
  font-size: 22px;
  height: 70px !important;
  padding: 0 20px 0 50px !important;
}

.time_select {
  background: #fff;
}

.time_select {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 70px;
  line-height: 70px;
  font-size: 18px;
  display: flex;
}
.time_start,
.time_end,
.month_select {
  margin-left: 20px;
  padding-left: 1.3rem;
}

.el-table--border::after,
.el-table--group::after,
.el-table::before {
  background: none;
}

.pswp__caption__center {
  text-align: center !important;
}

.demonstration {
  margin-left: 60px;
  margin-right: 30px;
}
</style>



