<template>
  <div class="innerbox">
    <h3>未处理或未恢复报警信息列表</h3>
    <div class="info_content">
      <ul class="bd">
        <li
          v-for="(list,index) of Lists.slice((currentPage-1)*pageSize,(currentPage)*pageSize)"
          :key="index"
        >
          <a href="javascript:;">
            <div class="infoContent">
              <div :class="`grade${list.alarmLevel} grade`"></div>
              <div class="lineMain">
                <span class="infoDate">{{list.startTime | datefmt('YYYY-MM-DD HH:mm:ss')}}</span>
                <span class="infoTitle">{{list.deviceName}} {{list.summary}}</span>
              </div>
              <div class="infoBody">
                <span>{{list.info}}</span>
                <div v-if=" list.isRestore == 0" class="floatRight">未恢复</div>
                <div v-else class="floatRight">已恢复</div>
                <div v-if="list.isDealState== 0" class="floatRight">未处理&nbsp;&nbsp;</div>
                <div v-else class="floatRight">已处理&nbsp;&nbsp;</div>
              </div>
            </div>
          </a>
        </li>
      </ul>
    </div>
    <div class="pagination">
      <!-- 分页器 -->
      <el-pagination
        align="right"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        background
        layout="prev, pager, next"
        :total="Lists.length"
        :page-size="pageSize"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  props: ["intervalTime"],
  data() {
    return {
      Lists: [], //报警信息列表
      currentPage: 1, //默认当前页为第1页
      pageSize: 10, //默认显示1条数据
      nowDate: ""
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    handleSizeChange(size) {
      this.currentPage = 1;
      this.pageSize = size;
    },
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
    },
    //请求数据
    getData() {
      let self = this;
      //开始时间，结束时间参数
      let parama = {
        dataAllBool: false,
        startTime: this.intervalTime[0],
        endTime: this.intervalTime[1]
      };
      self.$http
        .post("/apis/ismweb/AlarmRecord/alarmListNew", parama) //请求地址
        .then(function(res) {
          //给表格赋值
          self.Lists = res.data.data;
          console.log(self.Lists);
          // alert(res);
        })
        .catch(function(error) {
          console.log("错误问题是" + error);
        });
    }
  },
  watch: {
    intervalTime() {
      // alert(this.intervalTime)
      this.getData();
    }
  },
  created() {
    this.getData();
  }
};
</script>
<style scoped>
* {
  margin: 0 0 6px 0;
  padding: 0;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
/*信息列表的样式*/
.bd {
  padding: 0 30px;
  flex: 1;
  overflow: hidden;
  list-style: none;
  background: #fff;
  text-align: left;
}
.bd > li {
  height: 75px;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border-bottom: 1px solid #f6f5f8;
}
.bd > li > a {
  text-decoration: none;
}
.bd > li:hover {
  background: #f3fff2;
}
.infoContent {
  padding: 10px 30px 25px 30px;
  overflow: hidden;
  width: 100%;
}
.infoDate {
  /* font-weight: 600; */
  color: #4b97f5;
  font-family: "MicrosoftYaHei";
}
.infoTitle {
  margin-left: 90px;
  color: #0d0d0d;
}
.infoBody {
  font-size: 16px;
}
.infoBody > span {
  display: inline-block;
  width: 50%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #707070;
}
.el-pagination {
  margin: 20px auto;
}
/*右侧信息*/
.grade {
  height: 26px;
  width: 50px;
  float: right;
}
.grade1 {
  background: url(../../assets/grade1.png) no-repeat left center;
}
.grade2 {
  background: url(../../assets/grade2.png) no-repeat left center;
}
.grade3 {
  background: url(../../assets/grade3.png) no-repeat left center;
}
.grade4 {
  background: url(../../assets/grade4.png) no-repeat left center;
}
.floatRight {
  float: right;
}
.mr20 {
  margin-right: 20px;
}
.infoBody > div {
  color: #929292;
}
</style>




