<template>
  <div>
    <div class="innerbox">
      <h3>各站处理速度</h3>
      <div class="tablePd" style>
        <el-table
          :data="tableData"
          stripe
          height=""
          style="width: 100%; font-size:16px; "
          :header-row-style="{height:'3.44rem',color:'#0d0d0d',background:'#eef1f6',fontSize:'16px'}"
          :row-style="{height:'3.44rem'}"
          :header-cell-style="getRowClass"
          :default-sort="{prop: 'total', order: 'descending'}"
        >
          <el-table-column
            prop="name"
            label="车站名"
            align="center"
            width="110"
            height="3.44rem"
            style="{'font-size':'14px'}"
          ></el-table-column>
          <el-table-column prop="total" label="总平均时间" align="center"></el-table-column>
          <el-table-column label="平均时间" align="center">
            <el-table-column prop="level1" label="一级报警" align="center" style="font-size:16px; "></el-table-column>
            <el-table-column prop="level2" label="二级报警" align="center" style="font-size:16px; "></el-table-column>
            <el-table-column prop="level3" label="三级报警" align="center" style="font-size:16px; "></el-table-column>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="innerbox mtp125">
      <h3>各站报警处理及时率</h3>
      <div class="tablePd" style>
        <el-table
          :data="tableData2"
          stripe
          height=""
          style="width: 100%; font-size:16px; "
          :header-row-style="{height:'3.44rem',color:'#0d0d0d',fontSize:'18px'}"
          :row-style="{height:'3.44rem'}"
          :header-cell-style="{color:'#0d0d0d','font-size':'18px'}"
          :default-sort="{prop: 'total', order: 'descending'}"
        >
          <el-table-column
            prop="name"
            label="车站名"
            align="center"
            width="110"
            height="3.44rem"
            style="{'font-size':'14px'}"
          ></el-table-column>
          <el-table-column prop label="及时处理" align="center">
            <el-table-column
              prop="intimeCount"
              label="数量"
              align="center"
              cell-style="font-size: 28px"
              :sortable="true"
            ></el-table-column>
            <el-table-column prop="intimeRatio" label="比例" align="center" :sortable="true"></el-table-column>
          </el-table-column>
          <el-table-column label="未及时处理" align="center">
            <el-table-column prop="notIntimeCount" label="数量" align="center" :sortable="true"></el-table-column>
            <el-table-column prop="notIntimeRatio" label="比例" align="center" :sortable="true"></el-table-column>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["intervalTime"],
  data() {
    return {
      tableData: [],
      tableData2: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  watch: {
      intervalTime(){
        //处理速度
    this.getSpeekDeal();
    //及时处理率
    this.getInTime();
      }
  },
  created() {
    //处理速度
    this.getSpeekDeal();
    //及时处理率
    this.getInTime();
  },
  methods: {
    dateFormat: function(second) {
      var dd, hh, mm, ss;
      second = typeof second === "string" ? parseInt(second) : second;
      if (!second || second < 0) {
        return;
      }
      //天
      dd = (second / (24 * 3600)) | 0;
      second = Math.round(second) - dd * 24 * 3600;
      //小时
      hh = (second / 3600) | 0;
      second = Math.round(second) - hh * 3600;
      //分
      mm = (second / 60) | 0;
      //秒
      ss = Math.round(second) - mm * 60;
      if (Math.round(dd) < 10) {
        dd = dd > 0 ? "0" + dd + "天" : "";
      }
      if (Math.round(hh) < 10) {
        hh = "" + hh;
      }
      if (Math.round(mm) < 10) {
        mm = "" + mm;
      }
      if (Math.round(ss) < 10) {
        ss = "" + ss;
      }
      return dd + "" + hh + "时" + mm + "分" + ss + "秒";
    },

    // 给表单的表头添加背景颜色
    getRowClass({ row, column, rowIndex, columnIndex }) {
      if (
        (rowIndex === 1 && columnIndex === 0) ||
        columnIndex === 4 ||
        columnIndex === 8
      ) {
        return "color:#fff;font-weight: normal;font-size:18px;background:#fe4c4c";
      } else if (
        (rowIndex === 1 && columnIndex === 1) ||
        columnIndex === 5 ||
        columnIndex === 9
      ) {
        return "color:#fff;font-weight: normal;font-size:18px;background:#ffd34c";
      } else if (
        (rowIndex === 1 && columnIndex === 2) ||
        columnIndex === 6 ||
        columnIndex === 10
      ) {
        return "color:#fff;font-weight: normal;font-size:18px; background:#4b97f5";
      }
      // if(rowIndex === 1 && columnIndex===3||columnIndex===7||columnIndex===11){
      //     return 'background: #4b97f5 ;color:#fff;font-weight: normal;font-size:12px;border-right:1px solid #ddd;border-bottom:1px solid #ddd '
      // }
      else {
        return "";
      }
    },
    getSpeekDeal(){
      //各站处理速度
    this.$http
      .post("/apis/ismweb/AlarmCacl/dealTimeByStation",{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
      .then(function(res) {
        this.tableData=[];
        let resdata = res.data.data;
        for (let i of resdata) {
          let name = this.dbm[i.dbm];
          let total = this.dateFormat(i.countAlarmAll);
          let level1 = this.dateFormat(i.countAlarm1);
          let level2 = this.dateFormat(i.countAlarm2);
          let level3 = this.dateFormat(i.countAlarm3);
          let arr = {
            name: name,
            total: i.countAlarmAll,
            level1: i.countAlarm1,
            level2: i.countAlarm2,
            level3: i.countAlarm3
          };
          this.tableData.push(arr);
        }
      });
    },
    getInTime(){
        //各站报警处理及时率
    this.$http
      .get("/apis/ismweb/AlarmCacl/dealIntimeRateByStation")
      .then(function(res) {
        this.tableData2=[];
        let resdata = res.data.data;
        for (let i of resdata) {
          let name = this.dbm[i.dbm];
          let intimeCount = i.intimeCount;
          let intimeRatio = i.inTimeRate.toFixed(2) + "%";
          let notIntimeCount = i.notIntimeCount;
          let notIntimeRatio = i.notIntimeRate.toFixed(2) + "%";
          let arr = {
            name: name,
            intimeCount: intimeCount,
            intimeRatio: intimeRatio,
            notIntimeCount: notIntimeCount,
            notIntimeRatio: notIntimeRatio
          };
          this.tableData2.push(arr);
        }
      });
    }
  }
};
</script>
<style scoped>
.el-table th > .cell {
  font-weight: bold;
}

.el-table--border th {
  border-right: 1px solid #ddd !important;
  border-bottom: 1px solid #ddd;
}
.mtp125 {
  margin-top: 1.25rem;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.el-table__body tr,
.el-table__body td {
  padding: 0;
  height: 3.44rem;
}
#myChart {
  height: 23rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
  position: relative;
}
#myChart2 {
  height: 32rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
  margin-top: -30px;
  overflow-x: hidden;
}
.tablePd {
  padding: 0 3.81rem 2.44rem 3.81rem;
}
.tablePd span {
  color: #fff;
  display: inline-table;
  background-color: #8eb9f5;
  width: 21px;
  height: 21px;
  overflow: hidden;
}
.color1 {
  background-color: #f54545 !important;
}
.color2 {
  background-color: #ff8547 !important;
}
.color3 {
  background-color: #ffac38 !important;
}
.red {
  color: #fe4c4c !important;
  background: none !important;
}
.texBox {
  /* width: 50%; */
  margin: 15px auto 0 auto;
  padding-bottom: 15px;
  text-align: center;
}
.texBox p {
  position: relative;
  padding-left: 62px;
  font-size: 16px;
}
.texBox p::after {
  width: 18px;
  height: 18px;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg); /* Internet Explorer */
  -moz-transform: rotate(45deg); /* Firefox */
  -webkit-transform: rotate(45deg); /* Safari 和 Chrome */
  -o-transform: rotate(45deg); /* Opera */
  /* margin:50px auto;让菱形浏览器上居中   */
  content: "";
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 0;
}
.myChartBox {
  position: relative;
  height: 400px;
}
.txta {
  position: absolute;
  left: 20%;
  transform: translateX(-50%);
}
.txtb {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.txtc {
  position: absolute;
  left: 80%;
  transform: translateX(-50%);
}
.texd {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0px;
}
</style>



