<template>
  <div>
    <div class="innerbox">
      <h3>各站采集点超限统计</h3>
      <div class="tablePd" style>
        <el-table
          :data="tableData"
          stripe
          height="612.4"
          style="width: 100%; font-size:16px; "
          :header-row-style="{height:'3.44rem'}"
          :row-style="{height:'3.44rem'}"
          :header-cell-style="{background:'#eef1f6',color:'#0d0d0d','font-size':'18px'}"
          :default-sort="{prop: 'total', order: 'descending'}"
        >
          <el-table-column prop="name" label="车站名" align="center"></el-table-column>
          <el-table-column
            prop="count"
            label="总模拟量采集点数目"
            align="center"
            :sortable="true"
            :sort-method="sortByLevel1"
          >
            <template slot-scope="scope">
              <span class="red">{{ scope.row.count }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="overrunCount"
            label="超限采集点数目"
            align="center"
            :sortable="true"
            :sort-method="sortByLevel2"
          ></el-table-column>
          <el-table-column
            prop="addUp"
            label="超限累计次数"
            align="center"
            :sortable="true"
            :sort-method="sortByLevel3"
          ></el-table-column>
        </el-table>
      </div>
    </div>
    <div class="innerbox mtp125">
      <h3>全段道岔超限分析</h3>
      <el-row>
        <el-col :span="8">
          <div style="position: relative;">
            <div id="myChart1"></div>
            <div class="texBox texd">
              <p><span>◆</span>整体情况：道岔类故障比例偏高</p>
            </div>
          </div>
        </el-col>
        <el-col :span="16">
          <div class="tablePd" style>
            <el-table
              :data="daochaTB"
              stripe
              height="390.4"
              style="width: 100%; font-size:16px; "
              :header-row-style="{height:'3.44rem'}"
              :row-style="{height:'3.44rem'}"
              :header-cell-style="{background:'#eef1f6',color:'#0d0d0d','font-size':'18px'}"
              :default-sort="{prop: 'total', order: 'descending'}"
            >
              <el-table-column prop="devType" label="型号" align="center"></el-table-column>
              <el-table-column
                prop="count"
                label="总数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel1"
              >
                <template slot-scope="scope">
                  <span class="red">{{ scope.row.count }}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="overrunCount"
                label="超限数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel2"
              ></el-table-column>
              <el-table-column
                prop="badRate"
                label="占比（不良比）"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel3"
              ></el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="innerbox mtp125">
      <h3>全段信号机超限分析</h3>
      <el-row>
        <el-col :span="8">
          <div style="position: relative;">
            <div id="myChart2"></div>
            <div class="texBox texd">
              <p><span>◆</span>整体情况：道岔类故障比例偏高</p>
            </div>
          </div>
        </el-col>
        <el-col :span="16">
          <div class="tablePd" style>
            <el-table
              :data="xhjTB"
              stripe
              height="390.4"
              style="width: 100%; font-size:16px; "
              :header-row-style="{height:'3.44rem'}"
              :row-style="{height:'3.44rem'}"
              :header-cell-style="{background:'#eef1f6',color:'#0d0d0d','font-size':'18px'}"
              :default-sort="{prop: 'total', order: 'descending'}"
            >
              <el-table-column prop="devType" label="型号" align="center"></el-table-column>
              <el-table-column
                prop="count"
                label="总数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel1"
              >
                <template slot-scope="scope">
                  <span class="red">{{ scope.row.count }}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="overrunCount"
                label="超限数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel2"
              ></el-table-column>
              <el-table-column
                prop="badRate"
                label="占比（不良比）"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel3"
              ></el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="innerbox mtp125">
      <h3>全段轨道电路超限分析</h3>
      <el-row>
        <el-col :span="8">
          <div style="position: relative;">
            <div id="myChart3"></div>
            <div class="texBox texd">
              <p> <span>◆</span>整体情况：道岔类故障比例偏高</p>
            </div>
          </div>
        </el-col>
        <el-col :span="16">
          <div class="tablePd" style>
            <el-table
              :data="gddlTB"
              stripe
              height="390.4"
              style="width: 100%; font-size:16px; "
              :header-row-style="{height:'3.44rem'}"
              :row-style="{height:'3.44rem'}"
              :header-cell-style="{background:'#eef1f6',color:'#0d0d0d','font-size':'18px'}"
              :default-sort="{prop: 'total', order: 'descending'}"
            >
              <el-table-column prop="devType" label="型号" align="center"></el-table-column>
              <el-table-column
                prop="count"
                label="总数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel1"
              >
                <template slot-scope="scope">
                  <span class="red">{{ scope.row.count }}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="overrunCount"
                label="超限数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel2"
              ></el-table-column>
              <el-table-column
                prop="badRate"
                label="累计次数"
                align="center"
                :sortable="true"
                :sort-method="sortByLevel3"
              ></el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
// 引入基本模板
let echarts = require("echarts/lib/echarts");
// 引入柱状图组件
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/grid");
require("echarts/lib/component/legend");

export default {
  props: ["intervalTime"],
  data() {
    return {
      tableData: [],
      daochaTB: [],
      xhjTB: [],
      gddlTB: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  watch: {
      intervalTime(){
        this.getTabelData();
      }
  },
  created() {
    this.getTabelData();
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      this.$http
        .get("/apis/ismweb/AlarmCacl/overrunByDevType")
        .then(function(res) {
          let resdata = res.body.data;
          console.log(resdata)
          //道岔
          let daochaData = [["product", "全段道岔比例"]];
          let listDaocha = resdata.listDaocha;
          for (let item of listDaocha) {
            let arr = [item.dev, item.count];
            let badRate =
              ((item.overrunCount / item.count) * 100).toFixed(2) + "%";
            let arr2 = {
              devType: item.dev,
              count: item.count,
              overrunCount: item.overrunCount,
              badRate: badRate
            };
            this.daochaTB.push(arr2);
            daochaData.push(arr);
          }
          //信号机
          let listXHJ = resdata.listXHJ;
          let xhjData = [["product", "全段信号机比例"]];
          for (let item of listXHJ) {
            let arr = [item.dev, item.count];
            xhjData.push(arr);
            let badRate =
              ((item.overrunCount / item.count) * 100).toFixed(2) + "%";
            let arr2 = {
              devType: item.dev,
              count: item.count,
              overrunCount: item.overrunCount,
              badRate: badRate
            };
            this.xhjTB.push(arr2);
          }
          //轨道电路
          let gddlData = [["product", "全段轨道电路比例"]];
          let listTrackDev = resdata.listTrackDev;
          for (let item of listTrackDev) {
            let arr = [item.dev, item.count];
            gddlData.push(arr);
            let badRate =
              ((item.overrunCount / item.count) * 100).toFixed(2) + "%";
            let arr2 = {
              devType: item.dev,
              count: item.count,
              overrunCount: item.overrunCount,
              badRate: badRate
            };
            this.gddlTB.push(arr2);
          }

          // 基于准备好的dom，初始化echarts实例
          let myChart1 = echarts.init(document.getElementById("myChart1"));
          let myChart2 = echarts.init(document.getElementById("myChart2"));
          let myChart3 = echarts.init(document.getElementById("myChart3"));
          // 绘制图表
          var option1 = {
            legend: {
              orient: "vertical",
              x: "3%",
              show: false
            },
            tooltip: {},
            title: {
              text: "整体情况：全段一、二级报警比例偏高",
              x: "center",
              y: "bottom",
              show: false
            },
            dataset: {
              source: daochaData
            },
            // color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
            graphic: {
              type: "text",
              left: "center",
              top: "40%",
              style: {
                text: "",
                textAlign: "center",
                fill: "#333",
                fontSize: 20,
                fontWeight: 700
              }
            },
            series: [
              {
                type: "pie",
                name: "全段道岔比例",
                radius: "60%",
                center: ["50%", "50%"],
                encode: {
                  itemName: "product",
                  value: "全段道岔比例"
                }
              }
            ]
          };

          var option2 = {
            legend: {
              orient: "vertical",
              x: "3%",
              show: false
            },
            tooltip: {},
            title: {
              text: "整体情况：全段一、二级报警比例偏高",
              x: "center",
              y: "bottom",
              show: false
            },
            dataset: {
              source: xhjData
              // [
              //   ["product",  "本周报警比例"],
              //   ["道岔类", 11.3],
              //   ["信号机类", 18.8],
              //   ["轨道电路", 22.5],
              //   ["其他", 47.4]
              // ]
            },
            // color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
            graphic: {
              type: "text",
              left: "center",
              top: "40%",
              style: {
                text: "",
                textAlign: "center",
                fill: "#333",
                fontSize: 20,
                fontWeight: 700
              }
            },
            series: [
              {
                type: "pie",
                name: "全段信号机比例",
                radius: "60%",
                center: ["50%", "50%"],
                encode: {
                  itemName: "product",
                  value: "全段信号机比例"
                }
              }
            ]
          };

          var option3 = {
            legend: {
              orient: "vertical",
              x: "3%",
              show: false
            },
            tooltip: {},
            title: {
              text: "整体情况：全段一、二级报警比例偏高",
              x: "center",
              y: "bottom",
              show: false
            },
            dataset: {
              source: gddlData
              //  [
              //   ["product", "当天报警比例", "本周报警比例", "本月报警比例"],
              //   ["道岔类", 11.3, 30.4, 65.1, 53.3],
              //   ["信号机类", 18.8, 92.1, 85.7],
              //   ["轨道电路", 22.5, 67.2, 79.5],
              //   ["其他", 47.4, 67.1, 69.2, 72.4, 53.9, 39.1]
              // ]
            },
            // color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
            graphic: {
              type: "text",
              left: "center",
              top: "40%",
              style: {
                text: "",
                textAlign: "center",
                fill: "#333",
                fontSize: 20,
                fontWeight: 700
              }
            },
            series: [
              {
                type: "pie",
                name: "全段轨道电路比例",
                radius: "60%",
                center: ["50%", "50%"],
                encode: {
                  itemName: "product",
                  value: "全段轨道电路比例"
                }
              }
            ]
          };
          myChart1.setOption(option1);
          myChart2.setOption(option2);
          myChart3.setOption(option3);
          $(window).resize(function(){
            myChart1.resize();
            myChart2.resize();
            myChart3.resize();
        });
        });
    },
    sortByLevel1(obj1, obj2) {
      let val1 = obj1.count;
      let val2 = obj2.count;
      return val1 - val2;
    },
    sortByLevel2(obj1, obj2) {
      let val1 = obj1.level2;
      let val2 = obj2.level2;
      return val1 - val2;
    },
    sortByLevel3(obj1, obj2) {
      let val1 = obj1.level3;
      let val2 = obj2.level3;
      return val1 - val2;
    },
    sortByMonthlyRingRatio(obj1, obj2) {
      let val1 = obj1.MonthlyRingRatio;
      let val2 = obj2.MonthlyRingRatio;
      return val1 - val2;
    },
    sortByWeeklyRingRatio(obj1, obj2) {
      let val1 = obj1.WeeklyRingRatio;
      let val2 = obj2.WeeklyRingRatio;
      return val1 - val2;
    },
    indexMethod(index) {
      return index * 1;
    },
    getTabelData(){
    //各站采集点超限统计
    this.$http
      .post("/apis/ismweb/AlarmCacl/overrunByDevStation",{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
      .then(function(res) {
        this.tableData=[];
        // alert(1111111111111)
        let resdata = res.data.data;
        for (let i of resdata) {
          let arr = {
            name: this.dbm[i.dbm],
            count: i.count,
            overrunCount: i.overrunCount,
            addUp: i.addUp
          };
          this.tableData.push(arr);
        }
      });
    }
  }
};
</script>
<style scoped>
.mtp125 {
  margin-top: 1.25rem;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.el-table__body tr,
.el-table__body td {
  padding: 0;
  height: 3.44rem;
}
#myChart1,
#myChart2,
#myChart3 {
  height: 19.6rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
}
.tablePd {
  padding: 0 3.81rem 2.44rem 3.81rem;
}
.tablePd span {
  color: #fff;
  display: inline-table;
  background-color: #8eb9f5;
  /* width: 21px; */
  height: 21px;
  overflow: hidden;
}
.color1 {
  background-color: #f54545 !important;
}
.color2 {
  background-color: #ff8547 !important;
}
.color3 {
  background-color: #ffac38 !important;
}
.red {
  color: #fe4c4c !important;
  background: none !important;
}
.texBox p {
    padding-left: 32px;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.texBox span {
  color: #4B97F5;
  display:inline-block;
  font-size: 30px;
  margin-right:10px; 
}
.texd {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -35px;
  width: 100%;
}
</style>



