<template>
  <div class="innerbox">
    <div class="secA">
      <h3>全段系统综合统计</h3>
      <div class="main" style="width: 100%;height:75%;"></div>
      <p>
        <img src="../../assets/mark.png" />&nbsp;&nbsp;&nbsp;&nbsp;
        <span>整体情况：系统浏览度偏低,报警恢复率偏低</span>
      </p>
    </div>
    <div class="secB">
      <h3>各站统计详细信息</h3>
      <el-table
        el-table
        :data="tableData"
        stripe
        height="612.4"
        style="width: 90%; font-size:16px; margin:0 auto;"
        :header-row-style="{height:'3.44rem'}"
        :row-style="{height:'3.44rem'}"
        :header-cell-style="{background:'#F5F7FA',color:'#0d0d0d','font-size':'18px'}"
        :default-sort="{prop: 'rate', order: 'descending'}"
      >
        <el-table-column prop="stationName" label="车站名" align="center" width="110" height="3.44rem"></el-table-column>
        <el-table-column
          prop="dealrate"
          label="报警处理率"
          align="center"
          :sortable="true"
          sort-by="rate"
        ></el-table-column>
        <el-table-column prop="systemVisitRate" label="系统浏览度" align="center" sortable></el-table-column>
        <el-table-column prop="safetyRate" label="系统安全率" align="center" sortable></el-table-column>
        <el-table-column prop="watchTime" label="人员看守时间" align="center" sortable></el-table-column>
        <el-table-column prop="recoveryRate" label="报警恢复率" align="center" sortable></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/pie");
require("echarts/lib/chart/funnel");
require("echarts/lib/chart/line");
require("echarts/lib/component/title");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");
require("echarts/lib/component/legendScroll");
require("echarts/lib/chart/bar");
export default {
  props: ["intervalTime"],
  data() {
    return {
      screenWidth: document.body.clientWidth,
      tableData: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  created() {
    this.$http
      .post("/apis/ismweb/TestStationCacl/stationIndexValue",{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
      .then(function(res) {
        let resdata = res.data.data;
        // let name=resdata.data.data;
        for (let i of resdata) {
          let stationName = this.dbm[i.dbm];
          let dealrate =
            ((i.inTimeAlarmDeal / i.AlarmCount) * 100).toFixed(2) + "%";
          let systemVisitRate =
            ((i.browseCount / i.claimBrowseCount) * 100).toFixed(2) + "%";
          let safetyRate =
            (((i.devCount - i.errorDev) / i.devCount) * 100).toFixed(2) + "%";
          let watchTime = i.watchTime + "H";
          let recoveryRate =
            ((i.recoverAlarm / i.errorCount) * 100).toFixed(2) + "%";
          let arr = {
            stationName: stationName,
            dealrate: dealrate,
            systemVisitRate: systemVisitRate,
            safetyRate: safetyRate,
            watchTime: watchTime,
            watchTime: watchTime,
            recoveryRate: recoveryRate
          };
          this.tableData.push(arr);
        }
      });
  },

  mounted() {
    var _this = this;
    this.drawLine();
  },
  watch: {
      intervalTime(){
        alert(this.intervalTime)
      }
  },
  methods: {
    drawLine() {
      this.$http.get("/apis/ismweb/StationCacl/allIndexValue").then(res => {
        var saverate = res.data.data.devCount - res.data.data.errorDev;
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementsByClassName("main")[0]); //如果用id,header组件里的样式会影响这里
        // 绘制图表
        myChart.setOption(
          {
            backgroundColor: "#fff",
            title: {
              text: "",
              left: "50%",
              top: "85%",
              textAlign: "center",
              textStyle: {
                fontWeight: "normal",
                fontSize: "18",
                color: "#fff",
                textAlign: "center"
              }
            },
            tooltip: {},
            series: [
              {
                // title: "系统平均处理率",
                type: "pie",
                radius: [90, 105],
                center: ["10%", "50%"],
                color: "#1f5dea",
                label: {
                  normal: {
                    position: "center"
                  }
                },
                data: [
                  {
                    value: res.data.data.inTimeAlarmDeal,
                    name: "",
                    label: {
                      normal: {
                        formatter: "\n\n\n {d} %\n",
                        textStyle: {
                          fontSize: 30
                        }
                      }
                    },
                    tooltip: {
                      trigger: "item",
                      formatter: ""
                    }
                  },
                  {
                    value: res.data.data.AlarmCount,
                    name: "",
                    label: {
                      normal: {
                        formatter: "系统平均处理率\n\n",
                        textStyle: {
                          color: "#000",
                          fontSize: 18
                        }
                      }
                    },
                    itemStyle: {
                      normal: {
                        color: "#dfeaff"
                      },
                      emphasis: {
                        color: "#dfeaff"
                      }
                    }
                  }
                ]
              },
              //系统平均浏览度
              {
                name: "系统平均浏览度",
                type: "pie",
                radius: [90, 105],
                center: ["30%", "50%"],
                color: "#a51fea",
                label: {
                  normal: {
                    position: "center"
                  }
                },
                data: [
                  {
                    value: res.data.data.browseCount,
                    name: "系统平均浏览数",

                    label: {
                      normal: {
                        formatter: "\n\n\n {d} %\n",
                        textStyle: {
                          fontSize: 30
                        }
                      }
                    },
                    tooltip: {
                      trigger: "item",
                      formatter: ""
                    }
                  },
                  {
                    value: res.data.data.claimBrowseCount,
                    name: "",
                    label: {
                      normal: {
                        formatter: "系统平均浏览度\n\n",
                        textStyle: {
                          color: "#000",
                          fontSize: 18
                        }
                      }
                    },
                    itemStyle: {
                      normal: {
                        color: "#dfeaff"
                      },
                      emphasis: {
                        color: "#dfeaff"
                      }
                    }
                  }
                ]
              },
              {
                name: "系统平均安全率",
                type: "pie",
                radius: [90, 105],
                center: ["50%", "50%"],
                color: "#1ecde2",
                label: {
                  normal: {
                    position: "center"
                  }
                },
                data: [
                  {
                    value: saverate,
                    name: "",

                    label: {
                      normal: {
                        formatter: "\n\n\n {d} %\n",
                        textStyle: {
                          fontSize: 30
                        }
                      }
                    },
                    tooltip: {
                      trigger: "item",
                      formatter:
                        "{a} <br/>计算公式:占比率=({b}/系统平均安全总数)*100%<br/> 系统安全数 : {c}"
                    }
                  },
                  {
                    value: res.data.data.devCount,
                    name: "",
                    label: {
                      normal: {
                        formatter: "系统平均安全率\n\n",
                        textStyle: {
                          color: "#000",
                          fontSize: 18
                        }
                      }
                    },
                    itemStyle: {
                      normal: {
                        color: "#dfeaff"
                      },
                      emphasis: {
                        color: "#dfeaff"
                      }
                    }
                  }
                ]
              },
              {
                name: "看守人员数量",
                type: "pie",
                radius: [90, 105],
                center: ["70%", "50%"],
                color: "#43cb83",
                label: {
                  normal: {
                    position: "center"
                  }
                },
                data: [
                  {
                    value: res.data.data.watchTime,
                    name: "其他类型",

                    label: {
                      normal: {
                        formatter: "\n\n\n {d} %\n",
                        textStyle: {
                          fontSize: 30
                        }
                      }
                    },
                    tooltip: {
                      trigger: "item",
                      formatter:
                        "{a} <br/>计算公式:占比率=({b}/总看守时间)*100%<br/> 看守人员数 : {c}"
                    }
                  },
                  {
                    value: res.data.data.claimWatchTime,
                    name: "",
                    label: {
                      normal: {
                        formatter: "人员平均看守时间\n\n",
                        textStyle: {
                          color: "#000",
                          fontSize: 18
                        }
                      }
                    },
                    itemStyle: {
                      normal: {
                        color: "#dfeaff"
                      },
                      emphasis: {
                        color: "#dfeaff"
                      }
                    }
                  }
                ]
              },
              {
                name: "报警平均恢复率",
                type: "pie",
                radius: [90, 105],
                center: ["90%", "50%"],
                color: "#e21e1e",
                label: {
                  normal: {
                    position: "center"
                  }
                },
                data: [
                  {
                    value: res.data.data.errorCount,
                    name: "报警平均恢复率",

                    label: {
                      normal: {
                        formatter: "\n\n\n {d} %\n",
                        textStyle: {
                          fontSize: 30
                        }
                      }
                    },
                    tooltip: {
                      trigger: "item",
                      formatter:
                        "{a} <br/>计算公式:占比率=({b}/注销总数)*100%<br/> 到期注销数 : {c}"
                    }
                  },
                  {
                    value: res.data.data.recoverAlarm,
                    name: "",
                    label: {
                      normal: {
                        formatter: "报警恢复率\n\n",
                        textStyle: {
                          color: "#000",
                          fontSize: 18
                        }
                      }
                    },
                    itemStyle: {
                      normal: {
                        color: "#dfeaff"
                      },
                      emphasis: {
                        color: "#dfeaff"
                      }
                    }
                  }
                ]
              }
            ]
          },
          true
        );

        $(window).resize(function() {
          myChart.resize();
        });
      });
    }
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
/*环形图部分样式*/
.secA {
  height: 514px;
}
.secB {
  height: 739px;
  margin-top: 30px;
}
.secA,
.secB {
  /* width: 1651px ; */
  width: 100%;
  background: #fff;
}
/*表格部分样式*/
</style>



