<template>
  <div class="realtimealarm">
    <div class="hd">
      <!-- <router-link to='/' class="more">〉〉</router-link> -->
      <button class="more" @click="fullScreen">
        <img src="../assets/quanping.png" />
      </button>
      <button class="more" to="/" @click="enlarge">
        <img src="../assets/jia.png" />
      </button>
      <button class="more" @click="minimize">
        <img src="../assets/minus.png" />
      </button>
      <h3>实时警情示意图</h3>
    </div>
    <div class="bd">
      <div class="bd_marquee">
        <marquee>{{result.content}}</marquee>
      </div>
      <div class="bd_real">
        <!-- <img :src="result.alarmUrl" /> -->
        <svg xmlns="http://www.w3.org/2000/svg" version="1.1">
          
          <line x1="40%" y1="50" x2="200" y2="50" style="stroke:rgb(100,149,237);stroke-width:2" />
          <line x1="200" y1="50" x2="250" y2="100" style="stroke:rgb(100,149,237);stroke-width:2" />
          <circle cx="40%" cy="50" r="10" stroke="CornflowerBlue" stroke-width="2" fill="white" />
          <circle cx="200" cy="50" r="10" stroke="CornflowerBlue" stroke-width="2" fill="white" />
          <circle cx="250" cy="100" r="10" stroke="CornflowerBlue" stroke-width="2" fill="red" />
           <text x="30%" y="35" fill="white">长沙站</text>
           <text x="180" y="35" fill="white">暮云站</text>
           <text x="240" y="75" fill="white">株洲站</text>

        </svg> 

      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "realtimealarm",
  data() {
    return {
      result: ""
    };
  },
  methods: {
    enlarge() {
      // alert(111111)
      $("#realTimeAlarm").removeClass("fullScreen");
      $("#realTimeAlarm").addClass("large");
      // alert($("#ModC").height());
      $("#realTimeAlarm").height($("#ModC").height());
    },
    minimize() {
      $("#realTimeAlarm").removeClass("fullScreen");
      $("#realTimeAlarm").removeClass("large");
    },
    fullScreen() {
      $("#realTimeAlarm").removeClass("large");
      $("#realTimeAlarm").addClass("fullScreen");
      $("#realTimeAlarm").height("100%");
    }
  },
  created() {
    // axios.get("http://127.0.0.1:3000/realtimealarm/realTimeAlarm").then((res)=>{
    this.$http.get(`${this.baseURL}/ismweb/TestStationCacl/stationStatus`).then(res => {
      this.result = res.data.data;
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.more:focus,
.more:active:focus,
.more.active:focus,
.more.focus,
.more:active.focus,
.more.active.focus {
  outline: none;
  border-color: transparent;
  box-shadow: none;
}

.more {
  float: right;
  color: white;
  line-height: 42px;
  height: 42px;
  letter-spacing: -6px;
  margin-right: 10px;
  width: 20px;
  background: none;
  border: none;
}

.large {
  position: absolute;
  z-index: 9;
  left: 17.9%;
  width: 63.8%;
}

.fullScreen {
  width: 100%;
  position: fixed;
  z-index: 9;
  left: 0;
  top: 0;
  height: 100%;
}
/* .btn{    

    } */
.hd a {
  background: none;
}
.bd_marquee,
.bd_real,
.bd_real > img {
  margin: 0;
  padding: 0;
  border: 0;
}
.realtimealarm {
  height: 100%;
  display: flex;
  flex-flow: column;
}
.bd {
  flex: 1;
  display: flex;
  flex-flow: column;
}
.hd img {
  width: 80%;
}
.h4 {
  color: #fff;
  margin-left: 30%;
  margin-top: 15px;
}
.bd {
  width: 100%;
}
marquee {
  line-height: 2.5rem;
  height: 2.5rem;
  font-size: 1.06rem;
  font-family: "MicrosoftYaHei";
  color: #ffc100;
  background-color: rgba(28, 30, 43, 0.75);
  margin: 0 0.5%;
  /* opacity: 0.5; */
}
.bd_real {
  /* padding-top:0.5%; */
  width: 100%;
  /* background: url("../assets/realTimeWarning.png") no-repeat; */
  flex: 1;
  height: 100%;
  box-sizing: border-box;
}
.bd_real > img {
  width: 99%;
  height: 100%;
  /* vertical-align: 0%; */
  margin: 0 0.5%;
}
@media screen and (max-width: 768px) {
  .bd {
    height: 250px;
    padding: 0;
  }
}
</style>



