<template>
  <div class="operator">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>值班人员</h3>
    </div>
    <div class="bd">
      <ul class="bd_list">
        <li>
          <p>电务段：</p>
          <p v-if="list[0]">{{list[0].personReal}}</p>
        </li>
        <li>
          <img v-if="list[0]" :src="list[0].imgUrl" />
        </li>
        <li>
          <p>电务处：</p>
          <p v-if="list[1]">{{list[1].personReal}}</p>
        </li>
        <li>
          <img v-if="list[1]" :src="list[1].imgUrl" />
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "operator",
  data() {
    return {
      list: []
    };
  },
  methods: {
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/ComprehensiveStatistics/operatorDetails"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  },
  //以后使用这种方式获取接口数据
  created: function() {
    var self = this;
    // axios.get("http://localhost:3000/comprehensive/operator").then(function(res) {
    self.$http.get(`${this.baseURL}/ismweb/TestDutyPerson/dutyPersonReal`).then(function(res) {
      self.list = res.data.data;
    });
  }
};
</script>
<style scoped>
/* 公共样式 */
.operator {
  width: 100%;
  height: 100%;
  background: #3f4555;
}
.bd_list {
  display: flex;
  width: 80%;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 21px auto 34px;
}
.bd_list > li > img {
  width: 100%;
  cursor: pointer;
  transition: all 0.7s;
}
.bd_list > li > img:hover {
  width: 100%;
  cursor: pointer;
  transform: scale(1.3);
}
.bd_list > li > p:first-child {
  font-size: 18px;
  margin: 0 0 20px 0;
}
.bd_list > li:nth-child(2) {
  margin-right: 5%;
}
.bd_list > li:nth-child(3) {
  margin-left: 5%;
}
.bd_list > li > p:last-child {
  font-size: 14px;
}
.bd_list > li > p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
/* 当屏幕大于1920时 */
@media screen and (min-width: 1920px) {
  .bd_list > li > p:first-child {
    font-size: 18px;
    margin: 0 0 15px 0;
  }
  .bd_list > li:nth-child(2) {
    margin-right: 3%;
  }
  .bd_list > li:nth-child(3) {
    margin-left: 3%;
  }
  .bd_list > li > p:last-child {
    font-size: 15px;
  }
}
/* 当屏幕大于1600px，小于1920px时 */
@media screen and (min-width: 1600px) and (max-width: 1920px) {
  .bd_list > li > p:first-child {
    font-size: 16px;
    margin: 0 0 15px 0;
  }
  .bd_list > li:nth-child(2) {
    margin-right: 3%;
  }
  .bd_list > li:nth-child(3) {
    margin-left: 3%;
  }
  .bd_list > li > p:last-child {
    font-size: 13px;
  }
}
/* 当屏幕大于1200px，小于1600px时 */
@media screen and (min-width: 1200px) and (max-width: 1600px) {
  .bd_list > li > p:first-child {
    font-size: 14px;
    margin: 0 0 10px 0;
  }
  .bd_list > li:nth-child(2) {
    margin-right: 1%;
  }
  .bd_list > li:nth-child(3) {
    margin-left: 1%;
  }
  .bd_list > li > p:last-child {
    font-size: 12px;
  }
}
/* 当屏幕小于1200px，大于992px时 */
@media screen and (min-width: 992px) and (max-width: 1200px) {
  .bd {
    height: 217px;
  }
  .bd_list > li > p:first-child {
    font-size: 20px;
    margin: 0 0 20px 0;
  }
  .bd_list > li:nth-child(2) {
    margin-right: 3%;
  }
  .bd_list > li:nth-child(3) {
    margin-left: 3%;
  }
  .bd_list > li > p:last-child {
    font-size: 17px;
  }
  .bd_list > li > img {
    height: 129px;
    width: 103px;
  }
}
/* 当屏幕小于992px，大于768px时 */
@media screen and (min-width: 768px) and (max-width: 992px) {
  .bd_list > li > p:first-child {
    font-size: 20px;
    margin: 0 0 20px 0;
  }
  .bd_list > li:nth-child(2) {
    margin-right: 3%;
  }
  .bd_list > li:nth-child(3) {
    margin-left: 3%;
  }
  .bd_list > li > p:last-child {
    font-size: 17px;
  }
  .bd_list > li > img {
    height: 113px;
    width: 90px;
  }
}
/* 当屏幕小于768px时 */
@media screen and (max-width: 768px) {
  .bd_list {
    margin: 25px auto 23px;
  }
  .bd_list > li > p:first-child {
    font-size: 16px;
    margin: 0 0 10px 0;
  }
  .bd_list > li:nth-child(1),
  .bd_list > li:nth-child(3) {
    margin-right: 1%;
  }
  .bd_list > li:nth-child(2),
  .bd_list > li:nth-child(4) {
    margin-left: 0;
  }
  .bd_list > li > img {
    height: 90px;
    width: 72px;
  }
}
</style>


