<template>
  <div class="turnout-health">
    <div class="hd">
      <h3>道岔动作曲线健康状态</h3>
    </div>
    <div class="bd">
      <div id="myChart_turnoutHealth"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "turnout-health",
  data() {
    return {
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  methods: {
    drawLine() {
      // axios.get("http://127.0.0.1:3000/turnouthealth/turnoutHealth").then((res) => {
      axios.get(`${this.baseURL}/ismweb/Daocha/actionBadCount`).then(res => {
        var myChart_turnoutHealth = echarts.init(
          document.getElementById("myChart_turnoutHealth")
        );
        var option = {
          title: {
            text: "",
            x: "center",
            y: "top",
            textStyle: { fontSize: 16 }
          },
          tooltip: {
            trigger: "axis"
          },
          toolbox: {
            show: false
          },
          calculable: false,
          legend: {
            show: true,
            x: "center",
            y: "top",
            data: ["评估次数", "异常次数"],
            textStyle: { fontSize: 13, color: "white" }
          },
          toolbox: {
            show: false,
            feature: {
              mark: { show: true },
              dataView: { show: true, readOnly: false },
              magicType: {
                show: true,
                type: ["line", "bar", "stack", "tiled"]
              },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          grid: { show: "false", borderWidth: "0" },
          xAxis: [
            {
              type: "category",
              data: [
                this.dbm[res.data.data[0].dbm],
                this.dbm[res.data.data[1].dbm],
                this.dbm[res.data.data[2].dbm],
                this.dbm[res.data.data[3].dbm],
                this.dbm[res.data.data[4].dbm]
              ],
              axisLabel: {
                show: true,
                textStyle: {
                  color: "#ffffff", //更改坐标轴文字颜色
                  fontSize: 11 //更改坐标轴文字大小
                },
                interval: 0,
                rotate: -40
              }
            }
          ],
          yAxis: [
            {
              type: "value",
              name: "(次)",
              axisLabel: {
                show: true,
                textStyle: {
                  color: "#ffffff", //更改坐标轴文字颜色
                  fontSize: 13 //更改坐标轴文字大小
                }
              },
              splitLine: {
                show: false
              }
            }
          ],
          series: [
            {
              name: "评估次数",
              type: "bar",
              barGap: 0,
              symbol: "none",
              smooth: true,
              itemStyle: {
                normal: {
                  color: "#01bfeb"
                }
              },
              data: [
                res.data.data[0].daochaCount,
                res.data.data[1].daochaCount,
                res.data.data[2].daochaCount,
                res.data.data[3].daochaCount,
                res.data.data[4].daochaCount
              ]
            },
            {
              name: "异常次数",
              type: "bar",
              barGap: 0,
              symbol: "none",
              // smooth: true,
              itemStyle: {
                normal: {
                  color: "#f67890"
                }
              },
              data: [
                res.data.data[0].alarmCount,
                res.data.data[1].alarmCount,
                res.data.data[2].alarmCount,
                res.data.data[3].alarmCount,
                res.data.data[4].alarmCount
              ]
            }
          ]
        };
        myChart_turnoutHealth.setOption(option);
        //让echarts图表根据屏幕的尺寸来响应大小
        // window.onresize = myChart_ratio.resize;
        $(window).resize(function() {
          myChart_turnoutHealth.resize();
        });
      });
    }
  },
  mounted: function() {
    this.drawLine();
    $.extend($.fn, {
      renderCharts: function() {
        return this.each(function() {
          try {
            var $this = $(this);
            var script = $this.find("script");
            if (script.length == 0) return;
            var option = new Function(
              "return " + script.text().replace(/^\s/, "")
            )();
            //options.renderAsImage=true;
            var chart = echarts.init($this.get(0));
            chart.setTheme({});
            $(window).on("resize", function() {
              chart.resize();
            });
            chart.setOption(option);
          } catch (e) {
            // console.log(e);
          }
        });
      }
    });
    // load script
    var script = document.createElement("script");
    var src = "../static/echarts/build/dist/echarts-all.js";
    if ($(window).width() < 600) src = "../static/echarts/m/echarts-all.js";
    script.src = src;
    document.getElementsByTagName("head")[0].appendChild(script);
    (function render() {
      if (typeof echarts !== "object") {
        setTimeout(function() {
          render();
        }, 200);
        return;
      }
      $(".chart").renderCharts();
    }.call(this));
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.turnout-health {
  height: 49.35%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}
.bd {
  flex: 1;
}
.content {
  margin: 10px 0;
  border-right: 1px solid #555e75;
  border-left: 1px solid #555e75;
  height: 166px;
  overflow: hidden;
}
.h4 {
  color: #fff;
  margin-left: 30%;
  margin-top: 15px;
}

.txtStyle {
  text-align: center;
  color: #fff;
  margin-top: 15px;
}
.txtStyle span {
  color: #ffff00;
  font-size: 48px;
  margin-top: 15px;
}
#myChart_turnoutHealth {
  height: 90%;
  margin: 8% auto 0 auto;
  margin-left: -7%;
  width: 114%;
  page-break-inside: avoid;
}
@media (min-width: 768px) and (max-width: 1200px) {
  .bd {
    height: 250px;
    padding: 0 15%;
  }
}
@media (min-width: 480px) and (max-width: 768px) {
  .bd {
    height: 250px;
    padding: 0 10%;
  }
}
@media screen and (max-width: 480px) {
  .bd {
    height: 250px;
    padding: 0;
  }
}
</style>



