<template>
  <div class="device-fault mbt10">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>故障处理实时速度排行</h3>
    </div>
    <div class="bd">
      <div id="myChart_deviceFault"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "device-fault",
  data() {
    return {
      result: []
    };
  },
  mounted: function() {
    this.drawLine();
    $.extend($.fn, {
      renderCharts: function() {
        return this.each(function() {
          try {
            var $this = $(this);
            var script = $this.find("script");
            if (script.length == 0) return;
            var option = new Function(
              "return " + script.text().replace(/^\s/, "")
            )();
            //options.renderAsImage=true;
            var chart = echarts.init($this.get(0));
            chart.setTheme({});
            $(window).on("resize", function() {
              chart.resize();
            });
            chart.setOption(option);
          } catch (e) {
            // console.log(e);
          }
        });
      }
    });
    // load script
    var script = document.createElement("script");
    var src = "../static/echarts/build/dist/echarts-all.js";
    if ($(window).width() < 600) src = "../static/echarts/m/echarts-all.js";
    script.src = src;
    document.getElementsByTagName("head")[0].appendChild(script);
    (function render() {
      if (typeof echarts !== "object") {
        setTimeout(function() {
          render();
        }, 200);
        return;
      }
      $(".chart").renderCharts();
    }.call(this));
  },
  methods: {
    drawLine() {
      this.$http
        .get(`${this.baseURL}/ismweb/AlarmCacl/alarmCountListByYear`)
        .then(res => {
          var myChart_deviceFault = echarts.init(
            document.getElementById("myChart_deviceFault")
          );
          var option = {
            title: {
              //text:'年度故障次数',
              x: "center",
              y: "top",
              borderwidth:0,
              textStyle: { fontSize: 16 },
              textStyle: {
                //fontSize: 18,
                fontWeight: "normal",
                color: "#fff" // 主标题文字颜色
              }
            },
            
            tooltip: {
              trigger: "axis"
            },
            
            toolbox: {
              show: false
            },
            calculable: false,
            legend: {
              show: true,
              x: "center",
              y: "top",
              data: ["故障处理实时速度"],
              textStyle: {
                color: "#fff",
                fontSize: 14
              }
            },
            dataRange: {
              borderWidth: 0,  
            },
            markLine : {
              normal: {
                borderWidth: 0,  
              }
            },
            toolbox: {
              show: false,
              feature: {
                mark: { show: true },
                dataView: { show: true, readOnly: false },
                magicType: {
                  show: true,
                  type: ["line", "bar", "stack", "tiled"]
                  //  type: ["bar"]
                },
                restore: { show: true },
                saveAsImage: { show: true }
              }
            },
            grid: {show:'false',borderWidth:'0'},
            xAxis: [
              {
                type: "category",
                // boundaryGap: false,
                name: "",
                data: ["信号机", "道岔", "轨道电路", "其他"],
                axisLabel: {
                  textStyle: {
                    color: "#fff", //坐标值得具体的颜色
                    fontSize: 14
                  },
                  interval: 0
                  // rotate: -40
                },
                splitLine: {
                  show: true,
                  lineStyle: {
                    width: 0.5,
                    color: "#cccccc",
                    type: "solid"
                  }
                }
              }
            ],
            yAxis: [
              {
                type: "value",
                name: "(秒)",
                axisLabel: {
                  textStyle: {
                    color: "#fff" //坐标值的具体的颜色
                  },
                  formatter: "{value}"
                },
                splitLine: {
                  show: false
                }
              }
            ],
            series: [
              {
                name: "故障处理实时速度",
                type: "bar",
                barGap: 0,
                symbol: "none",
                 barWidth : 30,
                data: [
                  res.data.data.xhjCount,
                  res.data.data.daochaCount,
                  res.data.data.TrackDevCount,
                  res.data.data.restsCount
                ]
              }
            ]
          };
          myChart_deviceFault.setOption(option);
          //让echarts图表根据屏幕的尺寸来响应大小
          // window.onresize = myChart_ratio.resize;
          $(window).resize(function() {
            myChart_deviceFault.resize();
          });
        });
    },
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/alarm/SpeedRanking"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.device-fault {
  height: 49.35%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}
.bd {
  flex: 1;
}
.h4 {
  color: #fff;
  margin-left: 30%;
  margin-top: 15px;
}
.txtStyle {
  text-align: center;
  color: #fff;
  margin-top: 15px;
}
.txtStyle span {
  color: #ffff00;
  font-size: 48px;
  margin-top: 15px;
}
#myChart_deviceFault {
  height: 90%;
  margin: 8% auto 0 auto;
  margin-left: -10%;
  width: 120%;
  page-break-inside: avoid;
}
@media (min-width: 768px) and (max-width: 1200px) {
  .bd {
    height: 300px;
    padding: 2% 15%;
  }
}
@media (min-width: 480px) and (max-width: 768px) {
  .bd {
    height: 250px;
    padding: 0 10%;
  }
}
@media screen and (max-width: 480px) {
  .bd {
    height: 250px;
    padding: 0;
  }
}
</style>



