  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.photo_conent {
  /* height: 800px; */
  overflow-y: auto;
}
/*图片部分样式*/
.divStyle > .imageStyle > img:first-child {
  width: 100%;

  /* border-radius: 5px; */
}
.divStyle {
  float: left;
    margin: 2% 2.5% 1% 2.5%;
    width: 15%;
}
.divStyle > p {
     text-align: left;
    margin-top: 15px;
    font-size: 16px;
}
.divStyle > p:last-child {
  color: #acacac;
    font-size: 12px;
    margin-top: 0px;
}
.imageStyle {
  position: relative;
  transition: all 1s;
  cursor: pointer;
  padding: 6px;
  background: url(../../assets/photoFrame.png);
  background-size: 100%;
}
.imageStyle:hover {
  transform: scale(1.1);
}
.imageStyle p {
  position: absolute;
  right: 15px;
  bottom: 15px;
  color: #fff;
  background: #000;
  width: 49px;
  height: 24px;
  font-size: 14px;
  border-radius: 3px;
}
.photoFrame {
  position: absolute;
  left: -2px;
  top: -2px;
  width: 188px;
  height: 188px;
}
</style>



