<template>
  <div class="overrun-rate">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>电气特性超限报警率</h3>
    </div>
    <div class="bd">
      <div id="mychart_overrunRate"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "no-fault",
  data() {
    return {
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  mounted: function() {
    this.drawLine();
    $.extend($.fn, {
      renderCharts: function() {
        return this.each(function() {
          try {
            var $this = $(this);
            var script = $this.find("script");
            if (script.length == 0) return;
            var option = new Function(
              "return " + script.text().replace(/^\s/, "")
            )();
            //options.renderAsImage=true;
            var chart = echarts.init($this.get(0));
            chart.setTheme({});
            $(window).on("resize", function() {
              chart.resize();
            });
            chart.setOption(option);
          } catch (e) {
            // console.log(e);
          }
        });
      }
    });
    // load script
    var script = document.createElement("script");
    var src = "../static/echarts/build/dist/echarts-all.js";
    if ($(window).width() < 600) src = "../static/echarts/m/echarts-all.js";
    script.src = src;
    document.getElementsByTagName("head")[0].appendChild(script);
    (function render() {
      if (typeof echarts !== "object") {
        setTimeout(function() {
          render();
        }, 200);
        return;
      }
      $(".chart").renderCharts();
    }.call(this));
  },
  methods: {
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/alarm/overrunRate"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    },
    drawLine() {
      // axios.get("http://127.0.0.1:3000/alarm/overrunRate").then(res => {

      //axios.get("/apis/ismweb/AlarmCacl/rankingByOverrun?ssss=11&xx=2").then(res => {
      this.$http
        .post(`${this.baseURL}/ismweb/AlarmCacl/rankingByOverrun`, {
          startTime: "2019-07-01",
          endTime: "2019-07-21",
          isRecover: 1
        })
        .then(res => {
          console.log(res);
          // var resdata=res.data.data;data.sort((a, b) => a.english.charCodeAt(0) - b.english.charCodeAt(0));
          var resdata = res.data.data.sort(
            (a, b) => a.alarmCount - b.alarmCount
          );
          var mychart_overrunRate = echarts.init(
            document.getElementById("mychart_overrunRate")
          );
          var option = {
            title: {
              text: "",
              x: "center"
            },
            color: ["#f56c80"],
            tooltip: {
              trigger: "axis"
            },
            legend: {
              data: ["当月"],
              x: "center",
              y: "top",
              textStyle: {
                color: "#fff"
              }
            },
            toolbox: {
              show: false,
              feature: {
                mark: { show: true },
                dataView: { show: true, readOnly: false },
                magicType: {
                  show: true,
                  type: ["line", "bar", "stack", "tiled"]
                },
                restore: { show: true },
                saveAsImage: { show: true }
              }
            },
            grid: { show: "false", borderWidth: "0" }, //边框设置
            xAxis: [
              {
                type: "value",
                minInterval: 1,
                boundaryGap: [0, 0.01],
                name: "",
                axisLabel: {
                  textStyle: {
                    color: "#fff" //坐标值得具体的颜色
                  }
                },
                splitLine: {
                  show: true,
                  lineStyle: {
                    width: 0.9,
                    color: "#cccccc",
                    type: "solid"
                  }
                }
              }
            ],
            yAxis: [
              {
                type: "category",
                data: [
                  this.dbm[resdata[0].dbm],
                  this.dbm[resdata[1].dbm],
                  this.dbm[resdata[2].dbm],
                  this.dbm[resdata[3].dbm],
                  this.dbm[resdata[4].dbm]
                ],
                name: "",
                barCategoryGap: 10,
                axisLabel: {
                  textStyle: {
                    color: "#fff" //坐标值得具体的颜色
                  }
                },
                splitLine: {
                  show: false
                }
              }
            ],
            series: [
              {
                name: "当月",
                type: "bar",
                itemStyle: {
                  normal: {
                    barBorderRadius: 0
                  }
                },
                data: [
                  resdata[0].alarmCount,
                  resdata[1].alarmCount,
                  resdata[2].alarmCount,
                  resdata[3].alarmCount,
                  resdata[4].alarmCount
                ],
                barWidth: 14
              }
            ]
          };
          mychart_overrunRate.setOption(option);
          //让echarts图表根据屏幕的尺寸来响应大小
          // window.onresize = myChart_ratio.resize;
          $(window).resize(function() {
            mychart_overrunRate.resize();
          });
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.overrun-rate {
  height: 49.35%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}
.bd {
  flex: 1;
}
.content {
  margin: 10px 0;
  border-right: 1px solid #555e75;
  border-left: 1px solid #555e75;
  height: 166px;
  overflow: hidden;
}
.h4 {
  color: #fff;
  margin-left: 30%;
  margin-top: 15px;
}

.txtStyle {
  text-align: center;
  color: #fff;
  margin-top: 15px;
}
.txtStyle span {
  color: #ffff00;
  font-size: 48px;
  margin-top: 15px;
}
#mychart_overrunRate {
  min-height: 90%;
  margin: 8% auto 0 auto;
  margin-left: -5%;
  width: 120%;
  page-break-inside: avoid;
}
@media (min-width: 768px) and (max-width: 1200px) {
  .bd {
    height: 250px;
    padding: 0 15%;
  }
}
@media (min-width: 480px) and (max-width: 768px) {
  .bd {
    height: 250px;
    padding: 0 10%;
  }
}
@media screen and (max-width: 480px) {
  .bd {
    height: 250px;
    padding: 0;
  }
}
</style>



