<template>
  <div class="no-fault">
    <div class="hd">
      <h3>连续无故障天数</h3>
    </div>
    <div class="bd">
      <div class="div_1">连续无一般D类及以上故障天数:</div>
      <div class="div_2">
        <span>{{result}}</span>
        <span>天</span>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      result: ""
    };
  },
  created() {
    var self = this;
    // axios.get("http://localhost:3000/nofault/nofault").then(function(res) {
    axios.get(`${this.baseURL}/ismweb/TestAlarmCacl/noAlarmDayCount`).then(function(res) {
      self.result = res.data.data.count;
      // alert(res.data.count);
    });
  }
};
</script>
<style scoped>
.no-fault {
  width: 100%;
  height: 100%;
  background: #3f4555;
  /* border-bottom: 0.25rem solid #1d2027; */
}
.div_1,
.div_2 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  color: #fff;
}
.div_1 {
  font-size: 16px;
  margin-top: 8%;
}
.div_2 span:first-child {
  display: inline-block;
  font-size: 40px;
  /* margin-top: 5%; */
  color: #ffff00;
  cursor: pointer;
}
.div_2 span:last-child {
  font-size: 16px;
}
@media screen and (max-width: 1200px) {
  .div_1 {
    font-size: 20px;
    margin-top: 3%;
  }
  .div_2 span:first-child {
    font-size: 40px;
    margin-bottom: 3%;
  }
}
@media screen and (max-width: 768px) {
  .div_1 {
    font-size: 18px;
    margin-top: 5%;
  }
  .div_2 span:first-child {
    font-size: 30px;
    margin-bottom: 5%;
  }
}
</style>



