<template>
  <el-container>
    <el-header>系统综合统计</el-header>
    <el-container>
      <el-aside class="wp20">
        <aside :class="collapsed?'menu-collapsed':'menu-expanded'">
          <!--导航菜单-->

          <ul
            role="menu"
            class="el-menu el-menu--inline"
            data-old-padding-top
            data-old-padding-bottom
            data-old-overflow
            style="background-color: rgb(84, 92, 100);"
          >
          </ul>

          <el-menu
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            v-bind:style=" collapsed ? {} : { 'width': '' } "
            :default-active="$route.path"
            class="el-menu-vertical-demo"
            unique-opened
            router
            v-show="!collapsed"
          >
            <template v-for="(item,index) in menuData">
              <el-submenu :index="index+''" :key="index">
                <template slot="title">
                  <i :class="item.iconCls"></i>
                  {{item.name}}
                </template>
                <el-menu-item
                  v-for="child in item.children"
                  :index="child.path"
                  :key="child.path"
                >
                <div style="border-bottom: 1px solid #4f566a;">
                {{child.name}}
                </div>
                </el-menu-item>
              </el-submenu>
              <el-menu-item
                v-if="item.leaf&&item.children.length>0"
                :index="item.children[0].path"
                :key="item"
              >
                <i :class="item.iconCls"></i>
                {{item.children[0].name}}
              </el-menu-item>
            </template>
          </el-menu>
          <!-- 导航菜单-折叠后-->
          <ul
            class="el-menu el-menu-vertical-demo collapsed el-menu-collapsed"
            v-show="collapsed"
            ref="menuCollapsed"
          >
            <li v-for="(item,index) in menuData" class="el-submenu item" :key="index">
              <template v-if="!item.leaf">
                <div
                  class="el-submenu__title"
                  style="padding-left: 20px;"
                  @mouseover="showMenu(index,true)"
                  @mouseout="showMenu(index,false)"
                >
                  <i :class="item.iconCls"></i>
                </div>
                <ul
                  class="el-menu submenu"
                  :class="'submenu-hook-'+index"
                  @mouseover="showMenu(index,true)"
                  @mouseout="showMenu(index,false)"
                >
                  <li
                    v-for="child in item.children"
                    :key="child.path"
                    class="el-menu-item"
                    style="padding-left: 34px; color: #fff;"
                    :class="$route.path==child.path?'is-active':''"
                    @click="$router.push(child.path)"
                  >{{child.name}}</li>
                </ul>
              </template>
              <template v-else>
                <li class="el-submenu">
                  <div
                    class="el-submenu__title el-menu-item"
                    style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;"
                    :class="$route.path==item.children[0].path?'is-active':''"
                    @click="$router.push(item.children[0].path)"
                  >
                    <i :class="el-icon-s-home"></i>
                  </div>
                </li>
              </template>
            </li>
          </ul>
        </aside>
      </el-aside>
      <div class="wp80">
        <div class="time_select">
          <div class="block">
            <span class="demonstration">时间区段选择</span>
            <el-date-picker
              v-model="intervalTime"
              type="datetimerange"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              align="right"
              value-format="yyyy-MM-dd HH:mm:ss"
            ></el-date-picker>
          </div>
        </div>
        <el-main>
          <transition name="fade" mode="out-in">
            <router-view  v-bind:intervalTime="intervalTime"></router-view>
          </transition>
        </el-main>
      </div>
    </el-container>
  </el-container>
</template>

<script>
    let data = () => {
    return {
      sysName: "后台管理系统",
      menuData: [],
      collapsed: false,
      sysUserName: "管理员",
      activeName: "second",
      // tabPosition: 'left',
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
    },
    value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
    intervalTime: ""

    };
  };

  let initMenu = function() {
    for (let i in this.$router.options.routes_cs) {
      let root = this.$router.options.routes_cs[i];
      if (root.hidden) continue;
      let children = [];
      for (let j in root.children) {
        let item = root.children[j];
        if (item.hidden) continue;
        children.push(item);
      }

      if (children.length < 1) continue;
      this.menuData.push(root);
      root.children = children;
    }
  };

  export default {
    name: "home",
    data: data,

    methods: {
      initMenu: initMenu,
      //折叠导航栏
      collapse: function() {
        this.collapsed = !this.collapsed;
      },
      showMenu: function(i, status) {
        this.$refs.menuCollapsed.getElementsByClassName(
          "submenu-hook-" + i
        )[0].style.display = status ? "block" : "none";
      },
      handleClick(tab, event) {
        // console.log(tab, event);
      }
    },
    mounted: function() {
      this.initMenu();
    }
  };
</script>

<style scoped>
.el-menu {
  border: none !important;
}
.w100 {
  width: 100%;
  /* background: #fff; */
  max-width: 80.75%;
  /* max-height:92.25%; */
  background: rgb(246, 245, 248);
}
.time_select {
  background: #fff;
}

.el-aside {
  background-color: #3f4555;
  color: #fff;
  text-align: center;
  line-height: 200px;
  padding-left: 3px;
}

.el-main {
  background-color: #f6f6f6;
  color: #333;
  text-align: center;
  /* line-height: 160px; */
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
.home_icon {
  width: 25px;
  margin-bottom: 8px;
}
</style>



