<template>
  <div class="innerbox">
    <!-- 远程智能巡视照片列表头部,包括title,"所有照片"提示信息,选择,返回 -->
    <div class="header">
      <h3>远程智能巡视相册列表</h3>
      <span class="ptInfo">已选择{{photoCount}}张照片</span>
      <button class="chose" @click="chosePhoto">选择照片</button>
      <button class="back" @click="back">返回上一层</button>
    </div>
    <!-- 远程智能巡视照片列表内容 -->
    <div class="photo_content" id="gallery2">
      <!-- <a   v-for="(item,index) of photoList[0].photo.image" :key="index" class="divStyle"> -->
      <!-- <a :href="item"> -->
      <!-- <div class="imageStyle" @click="showLarge"> -->
      <!-- <img :src="item"  preview="1" preview-text='拍摄于2019年16：34：28&nbsp;&nbsp;&nbsp;&nbsp;img_48561231851231651531561'> -->
      <!-- <img class="check d_hidden" :src="checkSrc"> -->
      <!-- <img class="uncheck d_hidden" :src="uncheckSrc"> -->
      <!-- </div> -->
      <!-- </a> -->
      <!-- </a> -->

      <!-- <a v-for="(item,index) in photoList[0].images" :key="index" class="divStyle"> -->
      <a v-for="(item,index) in photoList" :key="index" class="divStyle">
        <!-- <a :href="item"> -->
        <div class="imageStyle" @click="showLarge">
          <img
            :src="item.imgUrl"
            preview="1"
            :preview-text="'拍摄于'+ item.shotTime + '&nbsp;&nbsp;&nbsp;&nbsp;img_'+ item.imgTime"
          />
          <img class="check d_hidden" :src="checkSrc" />
          <img class="uncheck d_hidden" :src="uncheckSrc" />
        </div>
        <!-- </a> -->
      </a>
    </div>
    <!-- 远程智能巡视照片尾部,包括全部选择、删除 -->
    <div class="footer d_hidden">
      <button class="select_all" @click="selectAll">全部选择</button>
      <button class="del" @click="del">删除</button>
    </div>
  </div>
</template>

<script>
import { dirname } from "path";
export default {
  data() {
    return {
      promptInfo: "所有照片",
      photoCount: 0,
      checkSrc: require("../../assets/checked.png"),
      uncheckSrc: require("../../assets/unchecked.png"),
      photoList: [],
      picList: []
    };
  },
  mounted: function() {},

  //获取接口数据
  beforeCreate: function() {
    var self = this;
    // axios.get("http://localhost:3000/remotetour/remotetour").then(function(res) {
    this.$http
      .post("/apis/ismweb/TestRemoteWatch/picGroupList?type=recent", {
        intervalTime: this.intervalTime
      })
      .then(function(res) {
        self.photoList = res.data.data;

        for (let item of res.data.data[0].images) {
          let shotTime = self.$moment
            .unix(item.imgTime)
            .format("YYYY年MM月DD日");

          let arr = {
            imgUrl: item.imgUrl,
            imgTime: item.imgTime,
            shotTime: shotTime
          };
          self.picList.push(arr);
        }
        self.photoList = self.picList;

        self.$previewRefresh(); //异步加载图片需要重新刷新，否则不生效。
        // console.log(res.data);
      });
  },

  methods: {
    //点击"选择照片"按钮,实现的方法
    chosePhoto() {
      //获取尾部div标签
      var footer = document.getElementsByClassName("footer")[0];
      var uncheck = document.getElementsByClassName("uncheck");
      var check = document.getElementsByClassName("check");
      //如果类名是footer d_hidden,就设置可见,并改变头部提示信息内容
      if (footer.className == "footer d_hidden") {
        footer.className = "footer d_visible";
        this.promptInfo = "已选择 " + this.photoCount + " 张照片";
        //选择照片初始状态
        for (var img of uncheck) {
          img.className = "uncheck d_visible";
        }
      } else {
        //反之不可见
        for (var img of uncheck) {
          img.className = "uncheck d_hidden";
        }
        for (var img of check) {
          img.className = "check d_hidden";
        }
        footer.className = "footer d_hidden";
        this.promptInfo = "所有照片";
        this.photoCount = 0;
      }
    },
    //点击图片实现的方法
    showLarge(event) {
      //如果当前尾部是隐藏状态,也就是未被选择状态,点击图片展开大图
      //alert(event.target);//parentNode.nextElementSibling.innerHTML
      var footer = document.getElementsByClassName("footer")[0];
      // var uncheck = document.getElementsByClassName("uncheck")[0];
      // var check = document.getElementsByClassName("check")[0];
      var check = event.target.nextElementSibling;
      var uncheck = event.target.nextElementSibling.nextElementSibling;
      if (footer.className == "footer d_hidden") {
        // alert("弹出大图");
      } else {
        //否则,点击图片为勾选或取消勾选
        if (check.className == "check d_hidden") {
          check.className = "check d_visible";
          uncheck.className = "uncheck d_hidden";
          this.photoCount++;
        } else {
          check.className = "check d_hidden";
          uncheck.className = "uncheck d_visible";
          this.photoCount--;
        }
      }
      event.stopPropagation();
    },
    //点击"全部选择"按钮
    selectAll() {
      var check = document.getElementsByClassName("check");
      //如果存在未被勾选的照片,点击"全选"后,完成全选,通过遍历所有照片,如果存在img[check]的class为check d_hidden,表示未全选,否则是全选
      //var flag = false;//如果全部没有被选中,为false,有一项被选中,为true
      var flag = 1;
      for (var img of check) {
        if (img.className == "check d_hidden") {
          //如果全被选中了,flag=1
          flag = 0;
          break;
        }
      }
      this.photoCount = 0;
      for (var img of check) {
        if (flag == 1) {
          //如果全部选中了,就全部取消选中
          img.className = "check d_hidden";
          img.nextElementSibling.className = "uncheck d_visible";
        } else {
          //否则,全部选中
          img.className = "check d_visible";
          img.nextElementSibling.className = "uncheck d_hidden";
          this.photoCount++;
        }
      }
    },
    //从数组中删除指定元素
    removeByValue(arr, val) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == val) {
          arr.splice(i, 1);
          break;
        }
      }
    },
    //
    removeByKey(arr, key) {
      for (var i = 0; i < arr.length; i++) {
        if (i == key) {
          arr.splice(arr[i], 1);
          break;
        }
      }
    },
    //获取当前路径的目录
    GetUrlPath() {
      var url = document.location.toString();
      if (url.indexOf("/") != -1) {
        url = url.substring(0, url.lastIndexOf("/"));
      }
      return url;
    },
    //删除功能
    del() {
      var check = document.getElementsByClassName("check");

      for (var i = 0; i < check.length; i++) {
        if (check[i].className == "check d_visible") {
          // console.log();
          // console.log(check[i].src);
          this.removeByKey(this.photoList[0].photo.image, i);
        }
      }
      for (var j = 0; j < check.length; j++) {
        check[j].className = "check d_hidden";
        check[j].nextElementSibling.className = "uncheck d_visible";
      }
      this.photoCount = 0;
    },
    //返回上一层
    back() {
      this.$router.push("./remotetour");
    }
  }
};

// window.onload=function(){$('#gallery2').rebox({ selector: 'a' });};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
img {
  width: 180px;
  height: 180px;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
/*头部样式*/
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.header {
  position: relative;
}
.ptInfo {
  position: absolute;
  left: 45%;
  top: 20px;
  font-size: 16px;
  font-family: "黑体";
}
.chose {
  position: absolute;
  width: 82px;
  height: 30px;
  left: 73%;
  top: 20px;
  font-size: 16px;
  background: #4b97f5;
  color: #fff;
  border: 0;
}
.back {
  position: absolute;
  width: 100px;
  height: 30px;
  left: 81.5%;
  top: 20px;
  font-size: 16px;
  background: #4b97f5;
  color: #fff;
  border: 0;
}
/*照片内容部分样式*/
.photo_content {
  /* height: 680px; */
  overflow-y: auto;
  width: 94%;
  margin: 20px 50px 0 100px;
}
.divStyle {
  float: left;
  margin: 28px 0 0 28px;
  position: relative;
}
/*照片尾部样式*/
.footer {
  position: relative;
  height: 40px;
  margin: 30px auto;
}
.select_all {
  position: absolute;
  width: 100px;
  height: 30px;
  left: 40%;
  top: 5px;
  font-size: 16px;
  background: #4b97f5;
  color: #fff;
  border: 0;
}
.del {
  position: absolute;
  width: 100px;
  height: 30px;
  left: 55%;
  top: 5px;
  font-size: 16px;
  background: #fe0000;
  color: #fff;
  border: 0;
}
.d_hidden {
  visibility: hidden;
}
.d_visible {
  visibility: visible;
}
.imageStyle {
  position: relative;
  cursor: pointer;
}
.check,
.uncheck {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 80%;
  left: 80%;
}

.rebox {
  /* cursor: pointer; */
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  /* -webkit-filter: none !important; */
  background: rgb(0, 0, 0); /* IE Fallback (Solid Colour) */
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAIElEQVQ4T2NkYGDYDMRkA8ZRAxhGw4BhNAyA+WAYpAMAIFgLQfO9BoEAAAAASUVORK5CYII=);
  background: rgba(0, 0, 0, 0.7);
}
.rebox * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.rebox-contents {
  position: absolute;
  top: 10%;
  left: 5%;
  text-align: center;
  width: 70%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
}
.rebox-contents .rebox-content {
  /* border: 5px solid #fff; */ /* box-shadow: 0 0 20px #000; */ /* border-radius: 1px; */
  max-width: 100%;
  max-height: 100%;
}
.rebox-loading {
  width: 31px;
  height: 31px;
  margin: -16px 0 0 -16px;
  position: absolute;
  top: 48%;
  left: 50%;
}
.rebox-caption {
  display: none;
  position: absolute;
  right: 5%;
  top: 10%;
  width: 20%;
  text-align: center;
  z-index: 1000;
  background: #fff; /* background: rgba(0,0,0,0.7); */
  height: 80%;
  padding-top: 4%;
  box-sizing: border-box;
}
.rebox-caption h1 {
  margin: 0 auto;
  max-width: 95%;
  display: block;
  *display: inline;
  *zoom: 1;
  padding: 10px;
  color: #505050;
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
  text-align: left;
}
.rebox-caption h2 {
  margin: 0 auto;
  max-width: 95%;
  display: block;
  *display: inline;
  *zoom: 1;
  padding: 0 10px;
  color: #000;
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
  text-align: left;
}

.rebox-caption p {
  margin: 0 auto;
  max-width: 95%;
  display: block;
  *display: inline;
  *zoom: 1;
  padding: 10px;
  color: #000;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
}

.rebox-button {
  position: absolute;
  z-index: 9999;
  min-width: 40px;
  height: 40px;
  line-height: 40px;
  background: rgb(0, 0, 0);
  opacity: 0.4;
  text-decoration: none;
  font-size: 24px;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  -webkit-border-radius: 32px;
  -moz-border-radius: 32px;
  -ms-border-radius: 32px;
  border-radius: 32px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
  /* margin-top: -20px; */
}
.rebox-button:hover,
.rebox-button:focus {
  opacity: 1;
  -webkit-transform: scale(1.4);
  -moz-transform: scale(1.4);
  -ms-transform: scale(1.4);
  transform: scale(1.4);
}
/* .rebox-close {right: 5%;top: 10%;margin-top: -20px;margin-right: -20px;background: rgba(0,0,0,1);}
.rebox-next {left: 40%;top: 10%;background: url(../images/icon-arrow-right.png)no-repeat  center;background-size: 10%;height: 80%;width: 35%;}
.rebox-prev {
    left: 5%;
    top: 10%;
    background: url(../images/icon-arrow-left.png)no-repeat center;
    background-size: 10%;
    height: 80%;
    width: 35%;
} */
.rebox-loading {
  left: 50%;
  top: 48%;
  -webkit-animation-name: spin;
  -webkit-animation-duration: 2000ms;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  -moz-animation-name: spin;
  -moz-animation-duration: 2000ms;
  -moz-animation-iteration-count: infinite;
  -moz-animation-timing-function: linear;
  -ms-animation-name: spin;
  -ms-animation-duration: 2000ms;
  -ms-animation-iteration-count: infinite;
  -ms-animation-timing-function: linear;
  animation-name: spin;
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@-ms-keyframes spin {
  from {
    -ms-transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
  }
}
@-moz-keyframes spin {
  from {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>



