<template>
  <div class="speed-ranking mbt10">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>设备类型故障</h3>
    </div>
    <div class="bd">
      <div id="myChart_sp"></div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
var echarts = require("echarts");
export default {
  data() {
    return {};
  },
  methods: {
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/alarm/DeviceFaultDetails"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    },
    drawRadar() {
      this.$http
        .get(`${this.baseURL}/ismweb/AlarmCacl/alarmCountListByYear`)
        .then(res => {
          var myChart_sp = echarts.init(document.getElementById("myChart_sp"));
          var option = {
            tooltip: {
              trigger: "item"
              //   formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            color: ["#0071c1", "#ffff00", "#ffc100", "#4eaf48", "#f56c80"],
            legend: {
              orient: "vertical",
              x: "center",
              y: "top",
              data: ["信号机", "道岔", "轨道电路", "其他"],
              textStyle: {
                color: "#fff",
                fontSize: 14
              },
              orient: "horizontal"
            },
            toolbox: {
              show: false,
              feature: {
                mark: { show: true },
                dataView: { show: true, readOnly: false },
                magicType: {
                  show: true,
                  type: ["pie", "funnel"],
                  option: {
                    funnel: {
                      x: "25%",
                      width: "50%",
                      funnelAlign: "center",
                      max: 1548
                    }
                  }
                },
                restore: { show: true },
                saveAsImage: { show: true }
              }
            },
            series: [
              {
                name: "排名",
                type: "pie",
                center: ["50%", "60%"],
                radius: ["48%", "70%"],
                itemStyle: {
                  normal: {
                    label: {
                      show: false,
                      position: "center"
                    },
                    labelLine: {
                      show: false
                    }
                  },
                  emphasis: {
                    label: {
                      show: true,
                      position: "center",
                      textStyle: {
                        fontSize: "22",
                        fontWeight: "bold"
                      }
                    }
                  }
                },
                data: [
                  { value: res.data.data.xhjCount, name: "信号机" },
                  { value: res.data.data.daochaCount, name: "道岔" },
                  { value: res.data.data.TrackDevCount, name: "轨道电路" },
                  { value: res.data.data.restsCount, name: "其他" }
                ]
              }
            ]
          };

          var currentIndex = -1;
          // console.
          let myChart = echarts.getInstanceByDom(
            document.getElementById("myChart_sp")
          );
          var dataLen = option.series[0].data.length;
          setInterval(() => {
            // 取消之前高亮的图形
            myChart.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: currentIndex
            });
            currentIndex = (currentIndex + 1) % dataLen;
            // 高亮当前图形
            myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: currentIndex
            });
            // 显示 tooltip
            myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: currentIndex
            });
          }, 2000);
          myChart_sp.setOption(option);
          $(window).resize(function() {
            myChart_sp.resize();
          });
        });
    }
  },
  mounted() {
    this.drawRadar();
  }
};
</script>
<style scoped>
.speed-ranking {
  height: 49.35%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}
.bd {
  flex: 1;
}
.content {
  margin: 10px 0;
  border-right: 1px solid #555e75;
  border-left: 1px solid #555e75;
  height: 166px;
  overflow: hidden;
}
.h4 {
  color: #fff;
  margin-left: 30%;
  margin-top: 15px;
}

.txtStyle {
  text-align: center;
  color: #fff;
  margin-top: 15px;
}
.txtStyle span {
  color: #ffff00;
  font-size: 48px;
  margin-top: 15px;
}
#myChart_sp {
  min-height: 80%;
  margin: 10% auto 0 auto; 
  page-break-inside: avoid;
}
@media (min-width: 768px) and (max-width: 1200px) {
  .bd {
    height: 250px;
    padding: 0 15%;
  }
}
@media screen and (max-width: 768px) {
  .bd {
    height: 250px;
    padding: 0;
  }
}
/* #myChart_sp {
  height: 200px;
  display: flex;
  justify-content: center;
  margin-top: 10px;
} */
</style>



