<template>
  <div class="info-list">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>即时报警信息</h3>
    </div>
    <div class="bd">
      <ul class="bd_list">
        <!-- <li v-for="list of lists.slice(0,8)" :key="list.id"> -->
        <li v-for="(list,index)  in lists" :key="index">
          <a href="#">
            <div :class="`grade${list.alarmLevel} grade`"></div>
            <div class="li_content">
              <div class="time">{{list.startTime | datefmt('YYYY-MM-DD HH:mm:ss')}} </div>
              <div class="v_md">
                <p>{{list.info}}</p>
              </div>
            </div>
            <span class="nub">{{index +1}}</span>
            <!-- {{list.ID}} -->
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      lists: []
    };
  },
  // created() {
  //   var self = this;
  //   axios.get("http://localhost:3000/infolist/infolist").then(function(res) {
  //     self.lists = res.data;

  //     // console.log(self.lists);
  //     console.log('所有报警信息'+res);
  //   });
  // },

  created() {
    var self = this;
    axios
      .post(`${this.baseURL}/ismweb/AlarmRecord/alarmListNew`)
      .then(function(res) {
        self.lists = res.data.data;
        // console.log('获取信息是')
        // console.log(res);
      })
      .catch(function(error) {
        console.log("错误问题是" + error);
      });
  },
  methods: {
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/infolist"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  }
};
</script>
<style scoped>
.info-list {
  width: 100%;
  height: 100%;
  background: #3f4555;
  /* border-bottom: 0.25rem solid #1d2027; */
}
.bd_list {
  padding: 0 2%;
  flex: 1;
  overflow: hidden;
}
.bd_list li {
  list-style: none;
  height: 12%;
  border-bottom: 2px solid #4b5160;
  position: relative;
  padding-left: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  box-sizing: border-box;
}
.bd_list li:hover {
  background: #353b48;
}
.bd_list a {
  text-decoration: none;
}
.li_content {
  display: flex;
  flex-flow: column;
  height: 100%;
}
.v_md {
  vertical-align: middle;
  height: 42px;
  padding-bottom: 5px;
  box-sizing: content-box;
  position: relative;
}
p {
  color: #ffffff;
  line-height: 21px;
  vertical-align: middle;
  font-size: 14px;
  /* height: 42px; */
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: left;
  position: absolute;
  transform: translateY(-50%);
  -ms-transform:translateY(-50%);
  -o-tranform:translateY(-50%);
  -webkit-transform:translateY(-50%);
  -moz-transform:translateY(-50%);
  top: 50%;
}
.time {
  color: #a5c7eb;
  font-size: 14px;
  display: flex;
  align-items: center;
  /* flex: 1; */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 12px;
}
.nub {
  width: 26px;
  height: 26px;
  border-radius: 13px;
  background: #0071c1;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  font-size: 16px;
  display: block;
  position: absolute;
  left: 10px;
  top: 30px;
}
.grade {
  height: 86px;
  width: 50px;
  float: right;
  margin-right: 10px;
}
.grade1,
.grade5 {
  background: url(../assets/grade1.png) no-repeat left center;
}
.grade2,
.grade6 {
  background: url(../assets/grade2.png) no-repeat left center;
}
.grade3,
.grade7 {
  background: url(../assets/grade3.png) no-repeat left center;
}
.grade4,
.grade8 {
  background: url(../assets/grade4.png) no-repeat left center;
}
</style>



