<template>
  <div class="alarm-toplist">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>报警车站排行</h3>
    </div>
    <div class="bd">
      <div class="at_content w90">
        <div class="at_1">
          <div class="w25">车站</div>
          <div class="w50">报警次数</div>
          <div class="w25">周环比</div>
        </div>
        <div class="at_2">
          <div class="item_row" v-for="i of lists" :key="i.value">
            <div class="w25">{{dbm[i.dbm]}}</div>
            <div class="w50">
              <div :class="`my_rating${i.countAlarmAll}`"></div>&nbsp;
              <span class="color_blue">{{i.countAlarmAll}}</span>
            </div>
            <div class="w25">
              <div v-if="i.countLastWeekCount==0">
                <span>--</span>
              </div>
              <div v-else>
                <span
                  v-if="(i.countAlarmAll-i.countLastWeekCount)>0"
                  class="color_red"
                >+{{(((i.countAlarmAll-i.countLastWeekCount)/i.countLastWeekCount)*100).toFixed(2)}}%</span>
                <span
                  v-else
                  class="color_green"
                >{{(((i.countAlarmAll-i.countLastWeekCount)/i.countLastWeekCount)*100).toFixed(2)}}%</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
let rating = require("../../static/jquery.star-rating-svg");
//   var rating = require(rating);

export default {
  name: "romote-tour",
  data() {
    return {
      lists: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  methods: {
    rating(params, itr) {
      params.starRating({
        totalStars: 8,
        emptyColor: "lightgray",
        hoverColor: "salmon",
        activeColor: "cornflowerblue",
        initialRating: itr,
        starSize: 13,
        strokeWidth: 0,
        readOnly: true,
        useGradient: false,
        callback: function(currentRating, $el) {
          alert("rated " + currentRating);
          // console.log('DOM Element ', $el);
        }
      });
    },
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/alarm/AlarmTopListDetails"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  },
  mounted() {
    // axios.get("http://127.0.0.1:3000/alarm/alarmToplist").then(res => {
    axios.get(`${this.baseURL}/ismweb/AlarmCacl/alarmRateListByLevel`).then(res => {
      // this.lists = res.data;
      var resdata = res.data.data.sort(
        (b, a) => b.countAlarmAll - a.countAlarmAll
      );
      this.lists = resdata;
      //先获取数据，再进行渲染
      this.$nextTick(() => {
        for (var item of this.lists) {
          this.rating($(".my_rating" + item.countAlarmAll), item.countAlarmAll);
        }
      });
    });
  }
};
</script>
<style>
@import "../../static/css/star-rating-svg.css";
</style>
<style scoped>
.alarm-toplist {
  overflow: hidden;
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  background: #3f4555;
  /* border-bottom: 0.25rem solid #1d2027; */
}
.color_red {
  color: #ff3000;
}
.color_green {
  color: #77ff89;
}
.color_blue {
  color: #18fcff;
}
.bd {
  flex: 1;
  color: #fff;
}
.at_1 {
  overflow: hidden;
  display: flex;
  height: 20%;
  align-items: center;
  border-bottom: 1px solid #363e53;
  font-size: 16px;
}
.at_2 {
  height: 80%;
  font-size: 14px;
}
.w25:last-child {
  text-align: right;
}
.w25 {
  width: 25%;
}
.w50 {
  width: 50%;
  display: flex;
  justify-content: center;
}
.w90 {
  width: 90%;
  margin: 0 auto;
  height: 100%;
}
.at_2,
.w25,
.w50,
.w90 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.item_row {
  display: flex;
  height: 18%;
  overflow: hidden;
  align-items: center;
}
@media screen and (min-width: 1200px) {
  .bd {
    padding: 0 1%;
  }
}
@media screen and (max-width: 1200px) {
  .bd {
    height: 250px;
    padding: 0;
  }
}
</style>




