<template>
  <div>
    <div class="innerbox ">
      <h3>各站报警情况</h3>
      <div style="     overflow: auto; margin:0; box-sizing: border-box;">
        <div id="myChart2"></div>
      </div>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6">
          <div class="text-left texBox">
            <p>
              <span class="spansty">◆</span>一级报警比例排行前三：广州站(
              <span class="red">25.1%</span>)、长沙站(
              <span class="red">23.6%</span>)、湘潭站(
              <span class="red">18.7%</span>)
            </p>
            <p>
              <span class="spansty">◆</span>一、二级报警比例排行前三：广州站(
              <span class="red">26.4%</span>)、长沙站(
              <span class="red">24.1%</span>)、湘潭站(
              <span class="red">19.9%</span>)
            </p>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="innerbox mtp125">
      <h3>全段报警比例</h3>
      <div class="myChartBox">
        <div class="txta">当天报警比例</div>
        <div class="txtb">本周报警比例</div>
        <div class="txtc">本月报警比例</div>
        <div id="myChart"></div>
        <div class="texBox texd">
          <p><span class="spansty">◆</span>整体情况：全段一、二级报警比例偏高</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { close } from "fs";
// 引入基本模板
let echarts = require("echarts/lib/echarts");
// 引入柱状图组件
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/chart/pie");
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/graphic");
require("echarts/lib/component/legend");
require("echarts/lib/component/title");

export default {
  props: ["intervalTime"],
  data() {
    return {
      tableData: [],
      dbm: {
        BZZ: "株洲",
        ACS: "长沙",
        CXT: "湘潭",
        BSZ: "深圳",
        AGZ: "广州",
        SDG: "东莞",
        LHZ: "惠州",
        LCZ: "郴州",
        DHY: "衡阳"
      }
    };
  },
  mounted() {
    this.drawLine();
  },
  watch: {
      intervalTime(){
        // alert(this.intervalTime)
        this.drawLine();
      }
  },
  methods: {
    drawLine() {
      let parama = {
        intervalTime: this.intervalTime,
      };
      this.$http
        .post(`${this.baseURL}apis/ismweb/AlarmCacl/alarmRateByLevel`,{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
        .then(function(res) {
            if(res.data.code==200){
                // alert(11111111111111)
                // 基于准备好的dom，初始化echarts实例
                let myChart = echarts.init(document.getElementById("myChart"));
                // 绘制图表
                var option = {
                  legend: { orient: "vertical", x: "3%" },
                  tooltip: {},
                  title: {
                    text: "整体情况：全段一、二级报警比例偏高",
                    // subtext: '纯属虚构',
                    x: "center",
                    y: "bottom",
                    show: false
                  },
                  dataset: {
                    source: [
                      ["product", "当天报警比例", "本周报警比例", "本月报警比例"],
                      [
                        "一级报警",
                        res.data.data.listDay.alarmLevel1,
                        res.data.data.listWeek.alarmLevel1,
                        res.data.data.listMonth.alarmLevel1
                      ],
                      [
                        "二级报警",
                        res.data.data.listDay.alarmLevel2,
                        res.data.data.listWeek.alarmLevel2,
                        res.data.data.listMonth.alarmLevel2
                      ],
                      [
                        "三级报警",
                        res.data.data.listDay.alarmLevel3,
                        res.data.data.listWeek.alarmLevel3,
                        res.data.data.listMonth.alarmLevel3
                      ],
                      [
                        "其他",
                        res.data.data.listDay.alarmLevel4,
                        res.data.data.listWeek.alarmLevel4,
                        res.data.data.listMonth.alarmLevel4
                      ]
                    ]
                  },
                  color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
                  graphic: {
                    type: "text",
                    left: "center",
                    top: "40%",
                    style: {
                      text: "",
                      textAlign: "center",
                      fill: "#333",
                      fontSize: 20,
                      fontWeight: 700
                    }
                  },
                  series: [
                    {
                      type: "pie",
                      name: "当天报警比例",
                      title: {
                        text: "当天报警比例"
                      },
                      radius: "60%",
                      center: ["20%", "50%"],
                      itemStyle: {
                        normal: {
                          label: {
                            show: true,
                            formatter: "{d}%"
                          },
                          labelLine: { show: true }
                        }
                      }
                    },
                    {
                      type: "pie",
                      name: "本周报警比例",
                      radius: "60%",
                      center: ["50%", "50%"],
                      encode: {
                        itemName: "product",
                        value: "本周报警比例"
                      }
                    },
                    {
                      type: "pie",
                      name: "本月报警比例",
                      radius: "60%",
                      center: ["80%", "50%"],
                      encode: {
                        itemName: "product",
                        value: "本月报警比例"
                      }
                    }
                  ]
                };

                myChart.setOption(option);
                $(window).resize(function() {
                  myChart.resize();
                });
                
            }else{
              console.log(res.msg)
            }
        })
        .catch((res)=>{
                console.log(res)
            });

      this.$http
        // .get("/apis/ismweb/AlarmCacl/alarmCountListByStation")
        .post(`${this.baseURL}apis/ismweb/AlarmCacl/alarmRateListByLevel`,{startTime: this.intervalTime[0],endTime: this.intervalTime[1]})
        .then(function(res) {
        //  alert(res.data.data.length);
          let allStation = [];
          let oneAlarm = [];
          let twoAlarm = [];
          let threeAlarm = [];
          let alarmCount = [];
          for (var i of res.data.data) {
            // console.log(i)
            allStation.push(this.dbm[i.dbm]);
            oneAlarm.push(i.countAlarm1);
            twoAlarm.push(i.countAlarm2);
            threeAlarm.push(i.countAlarm3);
            alarmCount.push(i.countAlarm4);
          }
          let myChart2 = echarts.init(document.getElementById("myChart2"));
          var option2 = {
            tooltip: {
              trigger: "axis",
              axisPointer: {
                // 坐标轴指示器，坐标轴触发有效
                type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
              }
            },
            legend: {
              data: ["一级报警", "二级报警", "三级报警", "其他"],
              show: false
            },
            color: ["#fe4c4c", "#ffd34c", "#4b97f5", "#d2d2d2"],
            grid: {
              left: "3%",
              right: "4%",
              // bottom: "3%",
              containLabel: true
            },
            xAxis: {
              type: "value",
              show: false
            },
            yAxis: {
              type: "category",
              data: allStation,
              
              // max:res.data.data.length-1,
              axisLine: {
                //y轴
                show: false,
                padding: '100'
              },
              axisTick: {
                //y轴刻度线
                show: false
              },
              axisLabel: {
                show: true,
                // padding: 12,
                textStyle: {
                  //更改坐标轴文字颜色
                  fontSize: 14 //更改坐标轴文字大小
                }
              }
              // max: 20
            },
            series: [
              {
                name: "一级报警",
                type: "bar",
                stack: "总量",
                barWidth: 25,
                label: {
                  normal: {
                    show: true,
                    position: "insideRight"
                  }
                },
                // data: [20, 30, 31, 24, 20, 33, 32]
                data: oneAlarm
              },
              {
                name: "二级报警",
                type: "bar",
                stack: "总量",
                barWidth: 20,
                label: {
                  normal: {
                    show: true,
                    position: "insideRight"
                  }
                },
                // data: [120, 132, 101, 134, 90, 230, 210]
                data: twoAlarm
              },
              {
                name: "三级报警",
                type: "bar",
                stack: "总量",
                barWidth: 20,
                label: {
                  normal: {
                    show: true,
                    position: "insideRight"
                  }
                },
                // data: [20, 182, 191, 234, 290, 330, 310]
                data: threeAlarm
              },
              {
                name: "其他",
                type: "bar",
                barCategoryGap: "50%",
                stack: "总量",
                barWidth: 20,
                label: {
                  normal: {
                    show: true,
                    position: "insideRight"
                  }
                },
                data: alarmCount
              }
            ]
          };
          // console.log(option2);
          myChart2.setOption(option2);
              $(window).resize(function() {
                myChart2.resize();
              });
        });
      },

    sortByLevel1(obj1, obj2) {
      let val1 = obj1.level1;
      let val2 = obj2.level1;
      return val1 - val2;
    },
    sortByLevel2(obj1, obj2) {
      let val1 = obj1.level2;
      let val2 = obj2.level2;
      return val1 - val2;
    },
    sortByLevel3(obj1, obj2) {
      let val1 = obj1.level3;
      let val2 = obj2.level3;
      return val1 - val2;
    },
    sortByMonthlyRingRatio(obj1, obj2) {
      let val1 = obj1.MonthlyRingRatio;
      let val2 = obj2.MonthlyRingRatio;
      return val1 - val2;
    },
    sortByWeeklyRingRatio(obj1, obj2) {
      let val1 = obj1.WeeklyRingRatio;
      let val2 = obj2.WeeklyRingRatio;
      return val1 - val2;
    },
    indexMethod(index) {
      return index * 1;
    }
  }
};
</script>
<style scoped>
.mtp125 {
  margin-top: 1.25rem;
}
.innerbox {
  background: #fff;
  overflow: hidden;
}
h3 {
  font-size: 18px;
  color: #0d0d0d;
  text-align: left;
  position: relative;
  font-weight: bold;
  padding-left: 20px;
  margin-left: 1.3rem;
  margin-bottom: 0;
  line-height: 3.44rem;
  height: 3.44rem;
}
h3::after {
  content: "";
  width: 0.25rem;
  height: 1.1rem;
  position: absolute;
  background: #499cf6;
  left: 0;
  display: inline-block;
  top: 1.17rem;
}
.el-table__body tr,
.el-table__body td {
  padding: 0;
  height: 3.44rem;
}
#myChart {
  height: 23rem;
  margin: 1% auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
  position: relative;
}
#myChart2 {
  height: 400px; 
  margin: -30px auto 0 auto;
  margin-left: 0;
  width: 100%;
  page-break-inside: avoid;
  /* margin-top: -30px; */
  overflow-x: hidden;
}
.tablePd {
  padding: 0 3.81rem 2.44rem 3.81rem;
}
.tablePd span {
  color: #fff;
  display: inline-table;
  background-color: #8eb9f5;
  width: 21px;
  height: 21px;
  overflow: hidden;
}
.color1 {
  background-color: #f54545 !important;
}
.color2 {
  background-color: #ff8547 !important;
}
.color3 {
  background-color: #ffac38 !important;
}
.red {
  color: #fe4c4c !important;
  background: none !important;
}
.texBox {
  /* width: 50%; */
  margin: -55px auto 0 auto;
  padding-bottom: 15px;
  text-align: center;
}

.texBox p {
   padding-left: 32px;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.texBox .spansty {
  color: #4B97F5;
  display:inline-block;
  font-size: 30px;
  margin-right:10px; 
}
.myChartBox {
  position: relative;
  height: 400px;
}
.txta {
  position: absolute;
  left: 20%;
  transform: translateX(-50%);
}
.txtb {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.txtc {
  position: absolute;
  left: 80%;
  transform: translateX(-50%);
}
.texd {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0px;
}
</style>



