<template>
  <el-container>
    <el-header>报警类统计</el-header>
    <el-container>
      <el-aside class="wp20">
        <aside :class="collapsed?'menu-collapsed':'menu-expanded'">
          <!--导航菜单-->
          <el-menu
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            v-bind:style=" collapsed ? {} : { 'width': '' } "
            :default-active="$route.path"
            class="el-menu-vertical-demo"
            unique-opened
            router
            v-show="!collapsed"
          >
            <template v-for="(item,index) of menuData">
              <el-submenu :index="index+''" :key="index">
                <el-menu-item v-for="child of item.children" :index="child.path" :key="child.path">
                  <div style="border-bottom: 1px solid #4f566a;">{{child.name}}</div>
                </el-menu-item>
              </el-submenu>
            </template>
          </el-menu>
        </aside>
      </el-aside>
      <div class="wp80">
        <!-- <div class="time_select">
          <div class="time_start">
            <span>开始时间 :&nbsp;&nbsp;&nbsp;</span>
            <el-date-picker
              v-model="startTime"
              align="right"
              type="date"
              placeholder="选择日期"
              :picker-options="pickerOptions"
            ></el-date-picker>
          </div>
          <div class="time_end">
            <span>结束时间 :&nbsp;&nbsp;&nbsp;</span>
            <el-date-picker
              v-model="endTime"
              align="right"
              type="date"
              placeholder="选择日期"
              :picker-options="pickerOptions"
            ></el-date-picker>
          </div>
          <div class="month_select">
            <el-tabs v-model="activeName" @tab-click="handleClick">
              <el-tab-pane style="font-size: 16px" label="当天" name="first"></el-tab-pane>
              <el-tab-pane label="本周" name="second"></el-tab-pane>
              <el-tab-pane label="本月" name="third"></el-tab-pane>
            </el-tabs>
          </div>
        </div>-->
        <div class="time_select">
          <div class="block">
            <span class="demonstration">时间区段选择</span>
            <el-date-picker
              v-model="intervalTime"
              type="datetimerange"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              align="right"
              value-format="yyyy-MM-dd HH:mm:ss"
            ></el-date-picker>
          </div>
        </div>
        <el-main>
          <transition name="fade" mode="out-in">
            <router-view v-bind:intervalTime="intervalTime"></router-view>
          </transition>
        </el-main>
      </div>
    </el-container>
  </el-container>
</template>
<style scoped>
.el-menu {
  border: none !important;
}
.w100 {
  width: 100%;
  /* background: #fff; */
}

.el-aside {
  background-color: #3f4555;
  color: #fff;
  text-align: center;
  line-height: 200px;
  padding-left: 3px;
}

.el-main {
  background-color: #f6f6f6;
  color: #333;
  text-align: center;
  /* line-height: 160px; */
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
.home_icon {
  width: 25px;
  margin-bottom: 8px;
}
</style>

<script>
let data = () => {
  //获取今天
  var nowdate = new Date(); //当天日期
  //今天是本周的第几天
  var nowdayofweek = nowdate.getDay();
  //当前日
  var nowday = nowdate.getDate();
  //当前月
  var nowmonth = nowdate.getMonth();
  //当前年
  var nowyear = nowdate.getFullYear();

  return {
    startTime: "",
    endTime: "",
    sysName: "后台管理系统",
    menuData: [],
    collapsed: false,
    sysUserName: "管理员",
    activeName: "second",
    pickerOptions: {
      shortcuts: [
        {
          text: "最近一周",
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "最近一个月",
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "最近三个月",
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit("pick", [start, end]);
          }
        }
      ]
    },
    value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
    intervalTime: [
      new Date(nowyear, nowmonth, nowday - nowdayofweek - 6),
      new Date()
    ]
  };
};

let initMenu = function() {
  for (let i in this.$router.options.routes_Alarm) {
    let root = this.$router.options.routes_Alarm[i];
    if (root.hidden) continue;
    let children = [];
    for (let j in root.children) {
      let item = root.children[j];
      if (item.hidden) continue;
      children.push(item);
    }

    if (children.length < 1) continue;
    this.menuData.push(root);
    root.children = children;
  }
};

export default {
  name: "home",
  data: data,
  methods: {
    initMenu: initMenu,
    //折叠导航栏
    collapse: function() {
      this.collapsed = !this.collapsed;
    },
    showMenu: function(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
        "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    },
    handleClick(tab, event) {
      // console.log(tab, event);
    }
  },
  mounted: function() {
    this.initMenu();
  }
};
</script>


