<template>
  <div class="failure-rate">
    <div class="hd">
      <h3>三大设备故障率</h3>
    </div>
    <div class="bd">
      <div id="myChart_failure"></div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
var echarts = require("echarts");
export default {
  data() {
    return {
      lists: []
    };
  },
  methods: {
    setFontSize(size) {
      var windowWidth = document.body.clientWidth;
      if (windowWidth > 1024 && windowWidth < 4000) {
        return (size * windowWidth) / 1920;
      } else if (windowWidth <= 800) {
        return (size * windowWidth) / 600;
      } else if (windowWidth < 1024) {
        return (size * windowWidth) / 800;
      } else if (windowWidth == 1024) {
        return (size * windowWidth) / 1366;
      } else if (windowWidth >= 4000) {
        return (size * windowWidth) / 1600;
      }
    },
    drawPie() {
      // axios.get('http://127.0.0.1:3000/failurerate/failurerate').then(function(res){
      this.$http.post(`${this.baseURL}/ismweb/TestAlarmCacl/badRateBy3dev`).then(function(res) {
        var myChart_failure = echarts.init(
          document.getElementById("myChart_failure")
        );
        var option = {
          series: [
            {
              name: "故障率",
              type: "pie",
              hoverAnimation: false, //鼠标悬停不放大
              radius: ["53%", "80%"],
              center: ["20%", "50%"],
              color: "#f56c80",
              label: {
                normal: {
                  position: "center"
                }
              },
              data: [
                {
                  value: res.data.data.daochaAlarmCount,
                  name: "发生故障道岔数",
                  label: {
                    normal: {
                      formatter: "\n\n {d}%\n",
                      textStyle: {
                        fontSize: 19,
                        color: "#96ce57" //环形中间百分比颜色
                      }
                    }
                  }
                },
                {
                  value: res.data.data.daochaCount,
                  name: "未发生故障道岔数",
                  label: {
                    normal: {
                      formatter: "道岔\n\n",
                      textStyle: {
                        color: "#fff", //环形中间文字颜色
                        fontSize: 15
                      }
                    }
                  },
                  itemStyle: {
                    normal: {
                      color: "#498bcc" //未选中时正常部分环形的颜色
                    },
                    emphasis: {
                      color: "#0071c1" //选中时正常部分环形颜色
                    }
                  }
                }
              ]
            },
            {
              type: "pie",
              hoverAnimation: false, //鼠标悬停不放大
              radius: ["53%", "80%"],
              center: ["50%", "50%"],
              color: "#f56c80",
              label: {
                normal: {
                  position: "center"
                }
              },
              data: [
                {
                  value: res.data.data.xhjAlarmCount,
                  name: "发生故障道信号机数",
                  label: {
                    normal: {
                      formatter: "\n\n {d}%\n",
                      textStyle: {
                        fontSize: 19,
                        color: "#96ce57"
                      }
                    }
                  }
                },
                {
                  value: res.data.data.xhjCount,
                  name: "未发生故障道信号机数",
                  label: {
                    normal: {
                      formatter: "信号机\n\n",
                      textStyle: {
                        color: "#fff",
                        fontSize: 15
                      }
                    }
                  },
                  itemStyle: {
                    normal: {
                      color: "#498bcc" //未选中时正常部分环形的颜色
                    },
                    emphasis: {
                      color: "#0071c1" //选中时正常部分环形颜色
                    }
                  }
                }
              ]
            },
            {
              type: "pie",
              hoverAnimation: false, //鼠标悬停不放大
              radius: ["53%", "80%"],
              center: ["80%", "50%"],
              color: "#f56c80",
              label: {
                normal: {
                  position: "center"
                }
              },
              data: [
                {
                  value: res.data.data.TrackDevAlarmCount,
                  name: "发生故障道轨道电路数",
                  label: {
                    normal: {
                      formatter: "\n\n {d}%\n",
                      textStyle: {
                        fontSize: 19,
                        color: "#96ce57"
                      }
                    }
                  }
                },
                {
                  value: res.data.data.TrackDevCount,
                  name: "未发生故障道轨道电路数",
                  label: {
                    normal: {
                      formatter: "轨道电路\n\n",
                      textStyle: {
                        color: "#fff",
                        fontSize: 15
                      }
                    }
                  },
                  itemStyle: {
                    normal: {
                      color: "#498bcc" //未选中时正常部分环形的颜色
                    },
                    emphasis: {
                      color: "#0071c1" //选中时正常部分环形颜色
                    }
                  }
                }
              ]
            }
          ]
        };
        myChart_failure.setOption(option);
        window.onresize = myChart_failure.resize;
      });
    }
  },
  mounted() {
    this.drawPie();
  }
};
</script>
<style scoped>
.failure-rate {
  width: 100%;
  height: 100%;
  background: #3f4555;
  /* border-bottom: 0.25rem solid #1d2027; */
}
.bd {
  flex: 1;
}
#myChart_failure {
  height: 140px;
  display: flex;
  justify-content: center;
  margin-top: 0;
}
@media screen and (max-width: 1200px) {
  #myChart_failure {
    height: 200px;
    margin: 25px auto;
    justify-content: center;
  }
}
@media screen and (max-width: 768px) {
  .bd {
    padding: 0;
  }
  #myChart_failure {
    height: 180px;
    margin-top: 10px;
  }
}
@media screen and (max-width: 480px) {
  .bd {
    padding: 0;
  }
  #myChart_failure {
    height: 140px;
    margin-top: 10px;
  }
}
</style>



