import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import HomePage from '@/components/HomePage'
import NoFault from '@/components/NoFault'
import FailureRate from '@/components/FailureRate'
import TurnoutHealth from '@/components/TurnoutHealth'
import RealTimeAlarm from '@/components/RealTimeAlarm'

//报警类统计 
import Alarm from '@/components/alarm/home'
import AlarmTopList from '@/components/alarm/AlarmTopListDetails'   //报警排行
import AlarmRatio from '@/components/alarm/AlarmRatioDetails'     //报警比例
import DeviceFault from '@/components/alarm/DeviceFaultDetails'   //设备类型故障
import SpeedRanking from '@/components/alarm/SpeedRankingDetails'    //故障处理率速度排行
import OverrunRate from '@/components/alarm/OverrunRateDetails'     //故障处理实时速度排行

// 远程智能巡视
import RemoteTour from '@/components/remotetour/home'
import RemoteTourChild from '@/components/remotetour/remotetour'
import CheckDetails from '@/components/remotetour/CheckDetails'

//系统综合统计
import ComprehensiveStatistics from '@/components/ComprehensiveStatistics/home'
import ComprehensiveStatisticsDetails from '@/components/ComprehensiveStatistics/ComprehensiveStatisticsDetails'    //系统综合统计
import Operator from '@/components/ComprehensiveStatistics/OperatorDetails'   //值班人员

//即使报警信息
import InfoList from '@/components/InfoList/home'
import NotRestored from '@/components/InfoList/NotRestored'   //未恢复
import untreated from '@/components/InfoList/untreated'     //未处理


Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: '首页',
      component: HomePage
    },
    {
      path: '/remotetour',
      name: '远程智能巡视',
      component: RemoteTour,
      children: [
        {
          path: '/remotetour/remotetour',
          name: '远程智能巡视',
          hidden: false,
          component: RemoteTourChild,
        },
        {
          path: '/remotetour/checkdetails',
          name: '远程智能巡视相册列表',
          hidden: false,
          component: CheckDetails
        }
      ], redirect: '/remotetour/remotetour'
    },
    {
      path: '/infolist',
      name: '及时报警信息 ',
      component: InfoList,
      children: [
        {
          path: '/infolist/untreated',
          name: '未处理报警',
          hidden: false,
          component: untreated
        }, {
          path: '/infolist/notrestored',
          name: '未恢复报警',
          hidden: false,
          component: NotRestored
        }
      ], redirect: '/infolist/untreated'
    },
    {
      path: '/alarm',
      name: '报警了统计',
      component: Alarm,
      children: [
        {
          path: '/alarm/AlarmTopListDetails',
          name: '报警车站排行',
          hidden: false,
          component: AlarmTopList
        },
        {
          path: '/alarm/alarmRatio',
          name: '各级报警比例',
          component: AlarmRatio
        },
        {
          path: '/alarm/DeviceFaultDetails',
          name: '设备类型故障',
          hidden: false,
          component: DeviceFault
        },
        {
          path: '/alarm/SpeedRanking',
          name: '故障处理实时速度排行',
          hidden: false,
          component: SpeedRanking
        },
        {
          path: '/alarm/overrunRate',
          name: '电气特性超限报警率',
          component: OverrunRate
        },
      ], redirect: '/alarm/AlarmTopListDetails'
    },
    {
      path: '/nofault',
      name: '无连续故障',
      component: NoFault
    },
    {
      path: '/failurerate',
      name: '三大故障率',
      component: FailureRate
    },
    {
      path: '/ComprehensiveStatistics',
      name: '系统综合统计',
      component: ComprehensiveStatistics,
      children: [
        {
          path: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails',
          name: '系统综合统计',
          hidden: false,
          component: ComprehensiveStatisticsDetails
        },
        {
          path: '/ComprehensiveStatistics/operatorDetails',
          name: '值班人员',
          component: Operator
        },
      ], redirect: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails'
    },
    {
      path: '/TurnoutHealth',
      name: '道岔实时健康状态',
      component: TurnoutHealth
    },
    {
      path: '/RealTimeAlarm',
      name: '实时警情示意图',
      component: RealTimeAlarm
    }
  ],

  routes_RemoteTour: [
    {
      path: '/remotetour',
      name: '远程智能巡视',
      component: RemoteTour,
      children: [
        {
          path: '/remotetour/remotetour',
          name: '远程智能巡视',
          hidden: false,
          component: RemoteTourChild
        },
        {
          path: '/remotetour/checkdetails',
          name: '远程智能巡视相册列表',
          hidden: true,
          component: CheckDetails
        }
      ], redirect: '/remotetour/remotetour'
    }
  ],
  routes_Alarm: [
    {
      path: '/alarm',
      name: '报警类统计',
      component: Alarm,
      children: [
        {
          path: '/alarm/AlarmTopListDetails',
          name: '报警车站排行',
          hidden: false,
          component: AlarmTopList
        }, {
          path: '/alarm/alarmRatio',
          name: '各级报警比例',
          hidden: false,
          component: AlarmRatio
        },
        {
          path: '/alarm/DeviceFaultDetails',
          name: '设备类型故障',
          hidden: false,
          iconCls: 'el-icon-s-home',
          component: DeviceFault
        },
        {
          path: '/alarm/SpeedRanking',
          name: '故障处理实时速度排行',
          hidden: false,
          component: SpeedRanking
        },
        {
          path: '/alarm/overrunRate',
          name: '电气特性超限报警率',
          hidden: false,
          component: OverrunRate
        },
      ], redirect: '/alarm/SpeedRanking'
    }
  ],
  routes_cs: [
    {
      path: '/ComprehensiveStatistics',
      name: '系统综合统计',
      component: ComprehensiveStatistics,
      children: [
        {
          path: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails',
          name: '系统综合统计详情',
          hidden: false,
          component: ComprehensiveStatisticsDetails
        },
        {
          path: '/ComprehensiveStatistics/operatorDetails',
          name: '值班人员',
          component: Operator
        }
      ], redirect: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails'
    }
  ],
  routes_infolist: [
    {
      path: '/infolist',
      name: '及时报警信息 ',
      component: InfoList,
      children: [
        {
          path: '/infolist/untreated',
          name: '未处理或未恢复报警',
          hidden: false,
          component: untreated
        }, {
          path: '/infolist/notrestored',
          name: '总报警信息列表',
          hidden: false,
          component: NotRestored
        }
      ], redirect: '/infolist/untreated'
    }
  ],
  // mode: 'history',
  srcollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  }
})



