<template>
  <div class="no-fault">
    <div class="hd">
      <router-link to="/" class="more" @click.native="seeShare"></router-link>
      <h3>系统综合统计</h3>
    </div>
    <div class="bd">
      <div id="myChart_comprehensive"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "no-fault",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted: function() {
    this.drawRadar();
    $.extend($.fn, {
      renderCharts: function() {
        return this.each(function() {
          try {
            var $this = $(this);
            var script = $this.find("script");
            if (script.length == 0) return;
            var option = new Function(
              "return " + script.text().replace(/^\s/, "")
            )();
            //options.renderAsImage=true;
            var chart = echarts.init($this.get(0));
            chart.setTheme({});
            $(window).on("resize", function() {
              chart.resize();
            });
            chart.setOption(option);
          } catch (e) {
            // console.log(e);
          }
        });
      }
    });
    // load script
    var script = document.createElement("script");
    var src = "../static/echarts/build/dist/echarts-all.js";
    if ($(window).width() < 600) src = "../static/echarts/m/echarts-all.js";
    script.src = src;
    document.getElementsByTagName("head")[0].appendChild(script);
    (function render() {
      if (typeof echarts !== "object") {
        setTimeout(function() {
          render();
        }, 200);
        return;
      }
      $(".chart").renderCharts();
    }.call(this));
  },
  methods: {
    drawRadar() {
      // axios.get("http://127.0.0.1:3000/comprehensive/comprehensive").then(function(res){
      this.$http
        .post(`${this.baseURL}/ismweb/TestStationCacl/allIndexValue`)
        .then(function(res) {
          // /ismweb/StationCacl/allIndexValue
          var newData = res.data.data;
          var myChart_comprehensive = echarts.init(
            document.getElementById("myChart_comprehensive")
          );
          var option = {
            title: {
              text: "",
              x: "center",
              //y:'30'
              //top: '5%',
              show: false
            },
            tooltip: {
              trigger: "axis"
            },
            legend: {
              orient: "vertical",
              x: "7%",
              y: "top",
              data: ["全段综合统计"],
              textStyle: {
                color: "#ffffff", // 图例文字颜色
                fontSize: 14
              }
            },
            toolbox: {
              show: false,
              feature: {
                mark: { show: true },
                dataView: { show: true, readOnly: false },
                magicType: {
                  show: true,
                  type: ["line", "bar", "stack", "tiled"]
                },
                restore: { show: true },
                saveAsImage: { show: true }
              }
            },
            polar: [
              {
                indicator: [
                  { text: "浏览度", max: newData.claimBrowseCount },
                  { text: "处理率", max: newData.AlarmCount },
                  { text: "看守时间", max: newData.claimWatchTime },
                  { text: "恢复率", max: newData.AlarmCount },
                  { text: "安全率", max: newData.devCount }
                ],
                radius: "70%",
                center: ["60%", "55%"],
                name: {
                  textStyle: {
                    color: "#fff",
                    fontSize: 14
                  }
                }
              }
            ],
            calculable: true,
            series: [
              {
                name: "系统综合统计",
                type: "radar",
                data: [
                  {
                    value: [
                      newData.browseCount,
                      newData.browseCount,
                      newData.watchTime,
                      newData.devCount - newData.recoverAlarm,
                      newData.errorDev
                    ],
                    name: "全段综合统计"
                  }
                  // ,
                  // {
                  //   value: [res.data[1].xtll, res.data[1].xtaq, res.data[1].bjhf, res.data[1].ryks, res.data[1].bjcl],
                  //   name: res.data[1].station
                  // }
                ]
              }
            ]
          };
          myChart_comprehensive.setOption(option);
          //让echarts图表根据屏幕的尺寸来响应大小
          // window.onresize = myChart_ratio.resize;
          $(window).resize(function() {
            myChart_comprehensive.resize();
          });
        });
    },
    seeShare() {
      let routeUrl = this.$router.resolve({
        path: "/ComprehensiveStatistics"
      });
      window.open(
        routeUrl.href,
        "_blank",
        "toolbar=yes, width=1424, height=750, top=200, left=200"
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content {
  margin: 10px 0;
  border-right: 1px solid #555e75;
  border-left: 1px solid #555e75;
  height: 166px;
  overflow: hidden;
}
.h4 {
  color: #fff;
  margin-left: 30%;
  margin-top: 15px;
}
.txtStyle {
  text-align: center;
  color: #fff;
  margin-top: 15px;
}
.txtStyle span {
  color: #ffff00;
  font-size: 48px;
  margin-top: 15px;
}
#myChart_comprehensive {
  min-height: 100%;
  margin: 0px auto;
  page-break-inside: avoid;
  padding: 0 !important;
}
@media only screen and (min-width: 1200px) {
  .bd {
    height: 217px;
    padding: 1% 5% 0 3%;
  }
}
@media (min-width: 768px) and (max-width: 1200px) {
  .bd {
    height: 300px;
    padding: 2% 15%;
  }
}
@media (min-width: 480px) and (max-width: 768px) {
  .bd {
    height: 250px;
    padding: 2% 10%;
  }
  .txtStyle > span {
    font-size: 38px;
  }
  #myChart_comprehensive {
    height: 100%;
    margin: 0 auto;
    page-break-inside: avoid;
  }
}
@media only screen and (max-width: 480px) {
  .bd {
    height: 250px;
    padding: 8% 10% 8% 0;
  }
}
</style>



